<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/good' }">商品管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改商品' : '添加商品' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="商品中文名" prop="name">
          <el-input size="small" style="width: 300px" v-model="ruleForm.name" maxlength="6" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="商品英文名" prop="englishName">
          <el-input size="small" style="width: 300px" v-model="ruleForm.englishName"></el-input>
        </el-form-item>
        <el-form-item label="商品图片" prop="fileListMain">
          <el-upload ref="uploadMainPicture"
                     class="picMain"
                     multiple :limit="1"
                     :file-list="ruleForm.fileListMain"
                     list-type="picture-card"
                     :action="uploadActionMain()"
                     :on-remove="handleRemoveMain"
                     :on-success="uploadSuccessMain"
                     :before-upload="beforeUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <div class="font_size">
            <!-- <span>支持jpg／png格式，建议尺寸： 450 * 80</span> -->
          </div>
        </el-form-item>
        <el-form-item label="商品详情图" prop="fileList">
          <el-upload
            class="pics"
            ref="uploadPictures"
            :file-list="ruleForm.fileList"
            multiple :limit="5"
            list-type="picture-card"
            :action="uploadAction()"
            :on-remove="handleRemove"
            :on-success="uploadSuccess"
            :before-upload="beforeUpload">
            <i class="el-icon-plus"></i>
          </el-upload>
          <div class="font_size">
            <!-- <span>支持jpg／png格式，建议尺寸： 450 * 80</span> -->
          </div>
        </el-form-item>
        <el-form-item label="冷热选择" prop="drinkType">
          <el-radio-group v-model="ruleForm.drinkType">
            <el-radio label="2">冷饮</el-radio>
            <el-radio label="1">热饮</el-radio>
            <el-radio label="0">全部</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="零售价(元)" prop="sellPrice">
          <el-input style="width: 300px" type="text" oninput="value=value.replace(/[^\d.]/g,'')" v-model="ruleForm.sellPrice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input style="width: 300px" :rows="4" type="textarea" placeholder="请输入内容" v-model="ruleForm.remark" maxlength="30" show-word-limit></el-input>
        </el-form-item>

        <!--<el-form-item class="stock_cont" label="料仓名">-->
          <!--<div class="row_item" v-for="(item, index) in ruleForm.productionProcess">-->
            <!--<span style="margin-right: 20px">顺序：{{ index + 1 }}</span>-->
            <!--<el-select v-model="item.material" placeholder="请选择">-->
              <!--<el-option v-for="option in options" :key="option.code" :label="option.name" :value="option.code"> </el-option>-->
            <!--</el-select>-->
            <!--<el-form-item label="延时(s)" prop="deferred">-->
              <!--<el-input size="small" @input="item.deferred = item.deferred.replace(/[^\d.]/g, '')" v-model="item.deferred"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="水量(s)" prop="water">-->
              <!--<el-input size="small" @input="item.water = item.water.replace(/[^\d.]/g, '')" v-model="item.water"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="下料(s)" prop="blanking">-->
              <!--<el-input size="small" @input="item.blanking = item.blanking.replace(/[^\d.]/g, '')" v-model="item.blanking"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-form-item label="搅拌(s)" prop="stir">-->
              <!--<el-input size="small" @input="item.stir = item.stir.replace(/[^\d.]/g, '')" v-model="item.stir"></el-input>-->
            <!--</el-form-item>-->
            <!--<el-link :class="index === 0 ? 'hidden' : ''" style="margin-left: 10px" :underline="false" type="primary" @click.stop="del(index)">删除</el-link>-->
            <!--<div :class="['move_btn', ruleForm.productionProcess.length === 1 ? 'hidden' : '']">-->
              <!--<i :disabled="index === 0" @click="$toTop(index, ruleForm.productionProcess)"><img src="@/assets/images/top.png"/></i>-->
              <!--<i :disabled="index === 0" @click="$toPrev(index, ruleForm.productionProcess)"><img src="@/assets/images/prev.png"/></i>-->
              <!--<i :disabled="index === ruleForm.productionProcess.length - 1" @click="$toNext(index, ruleForm.productionProcess)"><img src="@/assets/images/next.png"/></i>-->
              <!--<i :disabled="index === ruleForm.productionProcess.length - 1" @click="$toBottom(index, ruleForm.productionProcess)"><img src="@/assets/images/bottom.png"/></i>-->
            <!--</div>-->
          <!--</div>-->
          <!--<div>-->
            <!--<el-link :underline="false" type="primary" @click.stop="add">新增料仓</el-link>-->
          <!--</div>-->
        <!--</el-form-item>-->

        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        name: '',
        englishName: '',
        sellPrice:'',
        remark:'',
        fileListMain: [],
        fileList:[],
        picture:'',
        pictureDetail:'',
        drinkType: '0',
        // productionProcess: [{ deferred: 0, water: 0, blanking: 0, stir: 0, material: '' }],
      },
      rules: {
        name: [{ required: true, message: '请输入商品中文名', trigger: 'blur' }],
        englishName: [{ required: true, message: '请输入商品英文名', trigger: 'blur' }],
        fileListMain: [{ required: true, message: '请上传商品图片', trigger: 'blur' }],
        fileList: [{ required: true, message: '请上传商品详情图', trigger: 'blur' }],
        drinkType: [{ required: true, message: '请选择饮品温度', trigger: 'blur' }],
        sellPrice: [{ required: true, message: '请输入商品零售价', trigger: 'blur' }],
      },
      uploadImgMain: false,
      options: []
    }
  },

  methods: {
    // 监听关闭添加图片对话框
    handleImgClose(down) {
      this.$refs.upload.clearFiles()
      down()
    },

    handleRemoveMain(file, fileList) {
      console.log(fileList.length)
      console.log(this.ruleForm.fileListMain.length)
      if(fileList.length!=this.ruleForm.fileListMain.length){
        let fileName = file.response.data.fileName
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.fileListMain.length; i < len; i++) {
          if (this.ruleForm.fileListMain[i].response.data.fileName === fileName) {
            index = i
            break
          }
          console.log(index)
        }
        this.ruleForm.fileListMain.splice(index, 1)
      }

      if (fileList.length<5) {
        document.querySelector('.picMain .el-upload--picture-card').style.display = 'block'
      }
    },

    // 删除数组中图片
    handleRemove(file, fileList) {
      console.log(fileList.length)
      console.log(this.ruleForm.fileList.length)
      if(fileList.length!=this.ruleForm.fileList.length){
        let fileName = file.response.data.fileName
        // alert(1)
        // console.log(file)
        // let fileName = file.name
        let index = null
        for (let i = 0, len = this.ruleForm.fileListMain.length; i < len; i++) {
          if (this.ruleForm.fileListMain[i].response.data.fileName === fileName) {
            index = i
            break
          }
        }
        this.ruleForm.fileList.splice(index, 1)
        console.log(this.ruleForm.fileList)
      }
      if (fileList.length<5) {
        document.querySelector('.pics .el-upload--picture-card').style.display = 'block'
      }
    },

    // 上传图片成功
    uploadSuccessMain(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.fileListMain=fileList
      console.log(this.ruleForm.fileListMain)
      if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },

    // 上传图片成功
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.fileList=fileList
      if (this.ruleForm.fileList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
    },

    // 上传图片之前
    beforeUpload() {
      if (this.ruleForm.fileList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
      if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
    },
    uploadAction() {
      return this.$picUpload
      // return 'http://localhost:8888/api/img/'
    },

    uploadActionMain() {
      return this.$picUpload
      // return 'http://localhost:8888/api/img/'
    },

    beforeAvatarUpload(file) {
      this.isUpload = true
      const isJPG = file.type === 'image/jpeg' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG / png 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm);
        if (valid) {
            let params = Object.assign({}, this.ruleForm);
            if (params['fileList'] && params['fileList'].length > 0)
              params['pictureDetail'] =JSON.stringify(params['fileList'].map((o) => o.response.data.fileName).join(',').split(',')).toString();
            else
              delete params['pictureDetail'];
            if (params['fileListMain'] && params['fileListMain'].length > 0)
              params['picture'] = JSON.stringify(params['fileListMain'].map((o) => o.response.data.fileName).join(',').split(',')).toString();
            else
              delete params['picture'];
            // params.productionProcess = JSON.stringify(params.productionProcess)
            console.log(params)
            if(this.type =='add'){
              this.httpPost("/item/add",params).then(res => {
                this.$message.success('设置成功')
                  this.$router.push({
                    path: '/good',
                    query: {}
                  })
              })
                .catch(() => {
                  console.log('cancel')
                  return this.$message.error(res.message), false
                })
            }
            else{
              this.httpPost("/item/mod/"+params.id,params).then(res => {
                this.$message.success('设置成功'),
                  this.$router.push({
                    path: '/good',
                    query: {}
                  })
              })
                .catch(() => {
                  console.log('cancel')
                  return this.$message.error(res.message), false
                })
            }
        } else {
          console.log('error submit!!')
          return false
        }
      })

    },

    // 新增料仓
    // add() {
    //   this.ruleForm.productionProcess.push({ deferred: 0, water: 0, blanking: 0, stir: 0, value: '1' })
    // },

    // 删除料仓
    // del(index) {
    //   this.ruleForm.productionProcess.splice(index, 1)
    // },

    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    let queryForm = {
      size: 1000,
      page: 1,
      total: '',
      specialMaterial:0
    }
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else {
      var res = this.$route.params.info
        this.type = 'edit'
        this.ruleForm = this.$route.params.info
        this.ruleForm.fileListMain=[]
        this.ruleForm.fileList=[]
        this.ruleForm.drinkType = res.drinkType.toString()
        // this.$refs.quillEditor.getContent(this.ruleForm.introduce)
        if (res['picture']) {
          console.log(JSON.parse(res['picture']))
          JSON.parse(res['picture']).forEach(fn =>
            this.ruleForm.fileListMain.push({
              response: {
                data:{
                  fileName:fn
                }
              },
              url: this.$picDownload + fn
            })
          )
          if (this.ruleForm.fileListMain.length >= 1) document.querySelector('.picMain .el-upload--picture-card').style.display = 'none'
        }

        if (res['pictureDetail']) {
          console.log(JSON.parse(res['pictureDetail']))
          JSON.parse(res['pictureDetail']).forEach(fn =>
            this.ruleForm.fileList.push({
              response: {
                data:{
                  fileName:fn
                }
              },
              url: this.$picDownload + fn
            })
          )
          if (this.ruleForm.fileList.length >= 5) document.querySelector('.pics .el-upload--picture-card').style.display = 'none'
        }
    }
  }
}
</script>

<style lang="scss" scoped>
.hidden {
  visibility: hidden;
}
.row_item {
  display: flex;
  flex-wrap: wrap;
  /deep/.el-form-item__content {
    width: 100px;
  }
}
.move_btn {
  padding-top: 6px;
  margin-left: 50px;
  img {
    width: 20px;
    height: 20px;
    padding: 4px;
    border: 1px dashed #333;
    border-radius: 5px;
    margin: 0 5px;
    cursor: pointer;
  }
}
.stock_cont {
  /deep/.el-form-item__content {
    padding-top: 2px;
  }
}
</style>
