<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>料仓管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="120px" class="form_border">
        <el-form-item label="料仓名称" prop="name">
          <el-input v-model="queryForm.name" placeholder="请输入料仓名称"></el-input>
        </el-form-item>

        <el-form-item class="select_btn">
          <el-button type="primary" @click="search">搜索</el-button>
        </el-form-item>
      </el-form>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增料仓</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="code" label="编号"> </el-table-column>
          <el-table-column prop="name" label="料仓名称"> </el-table-column>
          <el-table-column prop="discharging" label="下料速度(g/s)">
            <template slot-scope="scope">
              {{ scope.row.discharging ? scope.row.discharging : '无' }}
            </template>
          </el-table-column>
          <el-table-column prop="alarm" label="报警量"> </el-table-column>
          <el-table-column prop="full" label="满量值"> </el-table-column>
          <el-table-column label="特殊材料">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.specialMaterial">是</el-tag>
              <el-tag type="info" v-else>否</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>

      <!-- 设置下料速度 -->
      <!--<el-dialog title="设置下料速度" :visible.sync="dialogVisible" width="30%">-->
      <!--<el-divider></el-divider>-->
      <!--<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">-->
      <!--<el-form-item label="下料速度" prop="discharging">-->
      <!--<el-input size="small" style="width: 300px" v-model="ruleForm.discharging"></el-input>-->
      <!--</el-form-item>-->
      <!--</el-form>-->
      <!--<div slot="footer" class="dialog-footer">-->
      <!--<el-button @click="dialogVisible = false">取 消</el-button>-->
      <!--<el-button type="primary" @click="saveForm('ruleForm')">确 定</el-button>-->
      <!--</div>-->
      <!--</el-dialog>-->
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        name: '',
        plateVal: '',
        size: 10,
        page: 1,
        total: ''
      },

      tableData: [],
      options: [],
      dialogVisible: false,

      formLabelWidth: '100px',
      ruleForm: {
        discharging: ''
      }
      // rules: {
      //   discharging: [{ required: true, message: '请输入下料速度', trigger: 'blur' }]
      // }
    }
  },
  methods: {
    // 搜索
    search() {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editStock', name: 'EditStock', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editStock', name: 'EditStock', params: { type: 'edit', info } })
    },

    editdischarging() {
      this.dialogVisible = true
    },
    // 保存
    // saveForm(formName) {
    //   this.$refs[formName].validate(valid => {
    //     if (valid) {
    //       console.log(this.ruleForm)
    //     } else {
    //       console.log('error submit!!')
    //       return false
    //     }
    //   })
    // },

    loadData() {
      this.httpGet('/silo/query/', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 240px !important;
  }
}
</style>
