<template>
  <div>
    <el-form ref="queryInfo" :model="queryInfo" label-width="120px" class="form_border">
      <el-form-item label="产品编号" prop="code">
        <el-input v-model="queryInfo.code" placeholder="请输入产品编号" clearable></el-input>
      </el-form-item>
      <el-form-item label="产品名称" prop="name">
        <el-input v-model="queryInfo.name" placeholder="请输入产品名称" clearable></el-input>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <el-button @click.stop="add" icon="el-icon-plus">新增产品</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="code" label="产品编号"> </el-table-column>
        <el-table-column prop="name" label="产品名称"> </el-table-column>
        <el-table-column prop="price" label="产品造价(元)" width="160"> </el-table-column>
        <el-table-column prop="memo" label="备注"> </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="220">
          <template slot-scope="scope">
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editBlender(scope.row)">搅拌机</el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editStorage(scope.row)">料仓</el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editGood(scope.row)">商品</el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="danger" @click.stop="del(scope.row.id)">停用</el-link>
            <el-link v-if="scope.row.state==0" :underline="false" type="primary" @click.stop="enable(scope.row.id)">启用</el-link>
            <!-- 上 下 架 -->
            <!--<el-link v-if="scope.row.shelveStatus === 1" type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row, 1)"><i style="color: #f00">下架</i></el-link>-->
            <!-- <el-link type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row)">选择机器</el-link> -->
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['tableData', 'queryForm'],
    data() {
      return {
        queryInfo: this.queryForm
      }
    },
    methods: {
      changePage() {
        this.$emit('getPage', this.queryInfo.page)
      },
      search() {
        this.$emit('getSearch', this.queryInfo)
      },
      add() {
        this.$emit('add')
      },
      edit(info) {
        this.$emit('edit', info)
      },
      editBlender(info) {
        this.$emit('editBlender', info)
      },
      editStorage(info) {
        this.$emit('editStorage', info)
      },
      editGood(info) {
        this.$emit('editGood', info)
      },
      del(id) {
        this.$emit('del', id)
      },
      enable(id) {
        this.$emit('enable', id)
      }
    },
    watch: {}
  }
</script>

<style lang="scss" scoped></style>
