<template>
  <div>
    <el-form ref="queryForm" :model="queryForm" label-width="100px" class="form_border">
      <el-form-item label="MAC地址" prop="deviceMac">
        <el-input v-model="queryForm.deviceMac" placeholder="请输入MAC地址"></el-input>
      </el-form-item>

      <el-form-item label="产品型号" prop="productId">
        <!-- <el-select clearable v-model="queryForm.productId" placeholder="请选择分组"> -->
        <!-- <el-option v-for="option in options" :key="option.id" :label="option.name" :value="option.id"> </el-option> -->
        <!-- </el-select> -->
        <el-autocomplete class="inline-input" v-model="productName" :fetch-suggestions="querySearch" @blur="mustSel(queryForm.productId, 'productName')" placeholder="请输入选择型号" @select="handleSelect" clearable></el-autocomplete>
      </el-form-item>

      <el-form-item label="在线状态" prop="online">
        <el-select v-model="queryForm.online" placeholder="请选择启用状态">
          <el-option label="全部" value=""> </el-option>
          <el-option label="在线" value="1"> </el-option>
          <el-option label="离线" value="0"> </el-option>
        </el-select>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>

    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div v-if="queryForm.isAll" class="btns">
        <el-button @click.stop="addDevice" icon="el-icon-plus">新增设备</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange" row-key="id">
        <!-- <el-table-column fixed type="selection" width="50" reserve-selection> </el-table-column> -->
        <el-table-column label="MAC地址" width="130">
          <template slot-scope="scope">
            <span ref="deviceMac">{{ scope.row.deviceMac }}</span>
          </template>
        </el-table-column>
        <el-table-column label="名称" prop="name" width="120">
          <template slot-scope="scope">
            {{ scope.row.name ? scope.row.name : '无' }}
          </template>
        </el-table-column>
        <el-table-column label="负责人" prop="employeeName" width="80">
          <template slot-scope="scope">
            <el-tag>{{ scope.row.employeeName ? scope.row.employeeName : '无' }}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="alarm" label="报警" width="80">
          <template slot-scope="scope">
            <el-link v-if="scope.row.alarm == 0" type="success" :underline="false">无报警</el-link>
            <el-link @click="alarmDevice(scope.row)" v-else type="danger">报警中</el-link>
          </template>
        </el-table-column>
        <el-table-column label="产品型号" prop="productName">
          <template slot-scope="scope">
            {{ scope.row.productName ? scope.row.productName : '无' }}
          </template>
        </el-table-column>
        <el-table-column prop="deviceAddress" label="地址">
          <template slot-scope="scope">
            {{ scope.row.deviceAddress ? scope.row.deviceAddress : '无' }}
          </template>
        </el-table-column>
        <el-table-column v-if="queryForm.isAll" prop="enterpriseName" label="所属公司"></el-table-column>
        <el-table-column prop="sellAmount" label="销售金额" width="120"></el-table-column>
        <el-table-column prop="refundAmount" label="退款金额" width="120"></el-table-column>
        <el-table-column prop="orderCount" label="订单数" width="120"></el-table-column>
        <el-table-column label="是否在线" width="80">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.online === 1">在线</el-tag>
            <el-tag type="info" v-else>离线</el-tag>
          </template>
        </el-table-column>
        <!--<el-table-column label="设备状态" width="100">-->
        <!--<template slot-scope="scope">-->
        <!--<el-tag v-if="scope.row.enable === 1">启用</el-tag>-->
        <!--<el-tag type="info" v-else>停用</el-tag>-->
        <!--</template>-->
        <!--</el-table-column>-->
        <!-- 操作 -->
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <!--<el-link type="primary" :underline="false" @click="edit(scope.row)">修改型号</el-link>-->
            <el-link v-if="!queryForm.isAll" type="primary" :underline="false" @click="editEmployee(scope.row)">负责人</el-link>
            <!-- <el-link v-if="queryForm.isAll" type="primary" :underline="false" @click="editEnterprise(scope.row)">设备归属</el-link> -->
            <!--<el-link type="primary" :underline="false" @click="alarmDevice(scope.row)">报警信息</el-link>-->
            <!-- 上 下 架 -->
            <el-link v-if="queryForm.isAll" type="primary" :underline="false" @click="editDevice(scope.row)">修改</el-link>
            <el-popover placement="right" width="300" trigger="click">
              <p>确定{{ scope.row.enable === 1 ? '停用' : '启用' }}此设备？</p>
              <div>{{ scope.row.plateTitle }}</div>
              <!-- <div style="font-size: 12px;color: rgb(252, 156, 0)">{{ scope.row.cnName }}</div> -->
              <div style="text-align: right; margin: 0">
                <el-button size="mini" type="text" @click="cancelInput(scope.row.id)">取消</el-button>
                <el-button type="primary" size="mini" @click="putaway(scope.row.enable, scope.row.id)">确定</el-button>
              </div>
              <el-link v-if="scope.row.enable === 1" type="primary" :underline="false" slot="reference"><i style="color: #f00">停用</i></el-link>
              <el-link v-else type="primary" :underline="false" slot="reference">启用</el-link>
            </el-popover>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)">尾页</span>
        <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>

    <!--修改归属公司 -->
    <el-dialog title="修改设备归属" :visible.sync="dialogVisible" width="25%">
      <el-divider></el-divider>

      <el-form ref="queryForm" :model="queryForm" label-width="100px">
        <el-form-item label="选择公司" prop="enterpriseId">
          <el-autocomplete class="inline-input" v-model="enterpriseName" :fetch-suggestions="queryEnterprise" placeholder="请输入公司名称" @blur="mustSel(queryForm.enterpriseId, 'enterpriseName')" @select="enterpriseHandleSelect" clearable></el-autocomplete>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="changeEnterprise">确 定</el-button>
      </div>
    </el-dialog>

    <!--修改设备负责人 -->
    <el-dialog title="选择设备负责人" :visible.sync="employeeDialogVisible" width="25%">
      <el-divider></el-divider>

      <el-form ref="queryForm" :model="queryForm" label-width="100px">
        <el-form-item label="选择负责人" prop="employeeId">
          <el-autocomplete class="inline-input" v-model="employeeName" :fetch-suggestions="queryEmployee" placeholder="请输入负责人名称" @blur="mustSel(queryForm.employeeId, 'employeeName')" @select="employeeHandleSelect" clearable></el-autocomplete>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="employeeDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="changeEmployee">确 定</el-button>
      </div>
    </el-dialog>

    <!--查看报警日志 -->
    <el-dialog title="报警信息" :visible.sync="detailDialog" width="80%">
      <el-divider></el-divider>
      <div v-for="(item, i) in detailData">
        <el-row>
          <el-col :span="2"> {{ i + 1 }}: </el-col>
          <el-col :span="4">
            {{ alarmType(item.alarmType) }}
          </el-col>
          <el-col :span="4">
            {{ item.alarmMemo || '' }}
          </el-col>
          <el-col :span="4">
            {{ item.handleUsername || '' }}
          </el-col>
          <el-col :span="4">
            {{ handleState(item.handleState) }}
          </el-col>
          <el-col :span="4">
            <!-- {{ dateFormat(item.updateDatetime) }} -->
            {{ dateFormat(item.updateDatetime) }}
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'queryForm'],
  data() {
    return {
      queryInfo: this.queryForm,
      multipleSelection: [],
      chooseGoodsStr: '',
      options: [],
      dialogVisible: false,
      // itemName:'',
      formLabelWidth: '100px',

      // 联想
      restaurants: [],
      // isSelect: false,
      groupSelect: {},
      productName: '',
      selectId: '',
      enterpriseId: '',
      enterpriseName: '',
      employeeId: '',
      employeeName: '',
      employeeDialogVisible: false,
      detailDialog: false,
      detailData: []
    }
  },
  computed: {
    alarmType() {
      return alarmType => {
        return this.$alarmType(alarmType)
      }
    },
    handleState() {
      return handleState => {
        return this.$handleState(handleState)
      }
    },
    dateFormat() {
      const that = this
      return data => {
        return this.$moment(data).format('YYYY-MM-DD HH:MM:SS')
      }
    }
  },
  methods: {
    // 弹出框确定

    // 确定上架
    putaway(enable, id) {
      const that = this
      if (enable == 1) {
        console.log(enable, id)
        that.httpPost('/device/stop/' + id, {}, null, true).then(ret => {
          that.$refs.deviceMac.click()
          that.$message.success('停用成功')
          that.$emit('getSearch')
        })
      } else {
        that.httpPost('device/start/' + id, {}, null, true).then(ret => {
          that.$refs.deviceMac.click()
          that.$message.success('启用成功')
          that.$emit('getSearch')
        })
      }
    },
    // 隐藏弹出框
    cancelInput(id) {
      console.log(id)
      console.log(this.$refs)
      this.$refs.deviceMac.click()
    },
    searchDetail(id) {
      const params = {
        deviceId: id
      }
      let url = '/device/alarm/query'
      if (this.queryForm.isAll) {
        url = '/device/alarm/query_all'
      }
      this.httpGet(url, params).then(res => {
        this.detailData = res.rows
      })
    },
    // 新增设备
    addDevice() {
      this.$router.push({ path: '/editdevice', name: 'EditDevice', params: { type: 'add'} })
    },
    // 修改设备
    editDevice(info) {
      // console.log(info)
      this.$router.push({ path: '/editdevice', name: 'EditDevice', params: { type: 'edit', info } })
    },
    //修改设备负责人
    editEmployee(row) {
      this.selectId = row.id
      this.employeeId = ''
      this.employeeName = ''
      this.employeeDialogVisible = true
    },
    //修改设备归属公司
    editEnterprise(row) {
      this.selectId = row.id
      this.enterpriseId = ''
      this.enterpriseName = ''
      this.dialogVisible = true
    },
    alarmDevice(row) {
      this.selectId = row.id
      this.searchDetail(row.id)
      this.detailDialog = true
    },
    handleSelectionChange(val) {
      // this.multipleSelection = val
      // this.chooseGoodsStr = ''
      // let arr = []
      // val.map(item => arr.push(item.id))
      // this.chooseGoodsStr = arr.join(',')
      // console.log(this.chooseGoodsStr)
    },
    handleSelect(item) {
      console.log(item)
      this.groupSelect = item
      this.productName = item.name
      this.queryForm.productId = item.id
      // this.isSelect = true // 是否选择了数据
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    },

    //负责人联想查询
    queryEmployee(queryString, cb) {
      this.associateQuery(queryString, cb, '/basic/employee/query', 'httpPost')
    },

    //公司联想查询
    queryEnterprise(queryString, cb) {
      this.associateQuery(queryString, cb, '/basic/enterprise/query', 'httpPost')
    },

    // 设备型号联想查询
    querySearch(queryString, cb) {
      this.associateQuery(queryString, cb, '/product/query/', 'httpGet')
    },

    //联想查询
    associateQuery(queryString, cb, url, method) {
      var params = { size: 10, page: 1, total: '', state: 1, name: queryString }
      this[method](url, params, null, true).then(res => {
        res.rows.map(i => (i.value = i.name))
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },
    enterpriseHandleSelect(item) {
      console.log(item)
      this.enterpriseName = item.name
      this.enterpriseId = item.id
    },
    employeeHandleSelect(item) {
      console.log(item)
      this.employeeName = item.code + ' - ' + item.name
      this.employeeId = item.id
    },
    mustSel(selId, selName) {
      this.$mustSel(this, selId, selName)
    },
    //改变设备归属公司
    changeEnterprise() {
      if (!this.enterpriseId) return this.$message.error('请先选择归属公司')
      var params = {
        deviceId: this.selectId,
        enterpriseId: this.enterpriseId
      }
      this.httpPost('/device/change', params).then(res => {
        this.$message.success('设置成功')
        this.dialogVisible = false
        this.search()
      })
    },
    //改变设备负责人
    changeEmployee() {
      if (!this.employeeId) return this.$message.error('请先选择负责人')
      var params = {
        deviceId: this.selectId,
        employeeId: this.employeeId
      }
      this.httpPost('/device/assign', params).then(res => {
        this.$message.success('设置成功')
        this.employeeDialogVisible = false
        this.search()
      })
    },
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },
    search() {
      if (!this.productName) this.queryForm.productId = ''
      this.$emit('getSearch', this.queryInfo)
    }
  },
  watch: {},
  mounted() {}
}
</script>

<style lang="scss" scoped>
.modal-cont {
  /deep/.el-form-item__content {
    & > div {
      width: 300px !important;
    }
  }
}
/deep/.el-dialog__body {
  .el-row {
    margin-bottom: 20px;
  }
}
</style>
