<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="出售中" name="selling">
          <GoodTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @getPutaway="getPutaway" @add="add" @edit="edit" @del="del" />
        </el-tab-pane>

        <el-tab-pane label="已停用" name="disuse">
          <GoodTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @getPutaway="getPutaway" @add="add" @edit="edit" @enable="enable" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import ChooseMachine from '@/components/choose-machine/ChooseMachine'
import GoodTable from '@/views/base/good/GoodTable'
export default {
  components: { ChooseMachine, GoodTable },
  data() {
    return {
      activeName: 'selling',
      queryForm: {
        name: '',
        englishName: '',
        state: 1, //1:出售中，0:已停用
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      editInfo: {},
      dialogVisible: false,
      formLabelWidth: '120px',
      deviceName: '',
      selectId: '', //  选机器按钮保存当前id
      selectDeviceName: '' // 返回已选名字str
    }
  },
  methods: {
    getPutaway(item) {
      this.selectId = item.id
      this.$refs.ChooseMachine.selected(item.deviceId)
    },
    getPage(page) {
      this.queryForm.page = page
      this.loadData()
    },
    getSearch(params) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editGood', name: 'EditGood', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editGood', name: 'EditGood', params: { type: 'edit', info } })
    },

    del(id) {
      this.$confirm('此操作将停用数据, 是否继续?', '提示', { type: 'warning' }).then(() => {
        this.httpPost('/item/disable/' + id, '')
          .then(ret => {
            this.$message({ type: 'success', message: '停用成功!' })
            this.handleClick()
          })
          .catch(() => {})
      })
    },

    enable(id) {
      this.$confirm('此操作将启用数据, 是否继续?', '提示', { type: 'warning' }).then(() => {
        this.httpPost('/item/enable/' + id, '')
          .then(ret => {
            this.$message({ type: 'success', message: '启用成功!' })
            this.handleClick()
          })
          .catch(() => {})
      })
    },

    // 选择后返回已选
    getName(e) {
      this.selectDeviceName = e
      console.log(this.selectId, this.selectDeviceName)
      let params = {
        itemId: this.selectId.toString(),
        deviceIds: this.selectDeviceName.toString()
      }
      this.httpPost('/item/shelve_on', params).then(ret => {
        this.$message({ type: 'success', message: '绑定成功!' })
        this.loadData()
      })
    },

    handleClick(tab, event) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    loadData() {
      let params = Object.assign({}, this.queryForm)
      params.state = this.activeName === 'selling' ? 1 : 0
      this.httpGet('/item/query', params).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  & > div {
    width: 240px !important;
  }
}
</style>
