<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/role' }">角色管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改角色' : '添加角色' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="角色名称" prop="name">
          <el-input size="small" style="width: 300px" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="角色编码" prop="code">
          <el-input size="small" style="width: 300px" v-model="ruleForm.code"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="memo">
          <el-input size="small" style="width: 300px" v-model="ruleForm.memo"></el-input>
        </el-form-item>
        <el-form-item label="资源列表" prop="resourceList">
          <el-tree ref="tree" :data="rightList" show-checkbox node-key="id" @check="checkChange" :default-checked-keys="checkList" :props="defaultProps" default-expand-all check-on-click-node> </el-tree>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// import menuList from '@/utils/menu'
export default {
  data() {
    return {
      ruleForm: {
        id: '',
        name: '',
        code: '',
        resourceList: [],
        memo: ''
      },
      restaurants: [],
      rules: {
        name: [{ required: true, message: '请输入角色名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入角色编码', trigger: 'blur' }]
      },
      defaultProps: {
        children: 'childNode',
        label: 'name'
      },
      rightList: [],
      checkList: [], // 得到的数据
      checkListAll: '' //  已改变的数据带有一级
    }
  },

  methods: {
    // 权限选择变化回调监听
    checkChange(currNode, checkedObj) {
      console.log(currNode, checkedObj)
      this.checkList = checkedObj.checkedKeys
      this.checkListAll = this.$roleJoin(checkedObj.checkedNodes)
    },

    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          this.ruleForm.resourceList = this.checkListAll
          let params = Object.assign({}, this.ruleForm)
          console.log(params)
          if (this.type == 'add') {
            this.httpPost('/basic/role/add', params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/role',
                  query: {}
                })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          } else {
            this.httpPost('/basic/role/mod/' + params.id, params)
              .then(res => {
                this.$message.success('设置成功'),
                  this.$router.push({
                    path: '/role',
                    query: {}
                  })
              })
              .catch(() => {
                console.log('cancel')
                return this.$message.error(res.message), false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    chooseResource() {
      this.$router.push({ path: '/editResource', name: 'EditResource', params: { type: 'add', info: this.ruleForm } })
    },
    // 返回
    cancelForm() {
      this.$router.back()
    }
  },

  mounted() {
    if (!this.$route.params.type) return this.$router.back()
    let params = {
      state: 1, //1:使用中，0:禁用中
      size: 999,
      page: 1,
      total: ''
    }
    this.httpPost('/basic/resource/query', params).then(res => {
      var menuList = []
      for (var i = 0; i < res.rows.length; i++) {
        if (res.rows[i].parentId == 0) {
          res.rows[i].childNode = []
          menuList.push(res.rows[i])
        }
      }
      const user = this.$sessionData('get', 'USER')
      for (var j = 0; j < menuList.length; j++) {
        for (var k = 0; k < user.resourceList.length; k++) {
          if (menuList[j].id == user.resourceList[k].parentId) {
            menuList[j].childNode.push(user.resourceList[k])
          }
        }
      }
      for (var i = 1; i < menuList.length; i++) {
        if (menuList[i].childNode.length == 0) {
          menuList.splice(i, 1)
          i--
        }
      }
      console.log(menuList)
      const menu = Object.assign([], menuList)
      this.menuArr = Object.assign([], menuList)
      this.rightList = menu
    })
    this.ruleForm = this.$route.params.info
    this.checkList = this.$route.params.info.resourceList
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else {
      this.type = 'edit'
      this.ruleForm = this.$route.params.info
      this.httpPost('/basic/role/query/resource/' + this.$route.params.info.id, '').then(res => {
        this.checkList = res
        this.ruleForm.resourceList = res
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
  .el-tree {
    width: auto !important;
    display: flex;
    padding-top: 8px;
    /deep/.el-tree-node {
      margin-right: 10px;
    }
  }
}
</style>
