<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/product' }">产品管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改产品' : '新增产品' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="产品编号" prop="code">
          <el-input size="small" v-model="ruleForm.code" clearable placeholder="请输入产品编号"></el-input>
        </el-form-item>
        <el-form-item label="产品名称" prop="name">
          <el-input size="small" v-model="ruleForm.name" clearable placeholder="请输入产名称"></el-input>
        </el-form-item>
        <el-form-item label="产品造价(元)" prop="sellPrice">
          <el-input type="text" @input="ruleForm.price = ruleForm.price.replace(/[^\d.]/g, '')" v-model="ruleForm.price" placeholder="" clearable placeholder="请输入产品价格"></el-input>
        </el-form-item>
        <el-form-item class="stock_cont" label="消杀时间">
          <div class="row_item" v-for="(item, index) in ruleForm.uvTime">
            <el-time-select v-model="item.start" :picker-options="{ start: '00:00', step: '00:15', end: '23:59' }" placeholder="开始时间"> </el-time-select>
            <el-time-select v-model="item.end" :picker-options="{ start: '00:00', step: '00:15', end: '23:59', minTime: item.start }" placeholder="结束时间"> </el-time-select>
            <el-link :class="ruleForm.uvTime.length === 1 ? 'hidden' : ''" style="margin-left: 10px" :underline="false" type="primary" @click.stop="del(index)">删除</el-link>
          </div>
          <div>
            <el-link :underline="false" type="primary" @click.stop="addVuTime">新增消杀时间</el-link>
          </div>
        </el-form-item>
        <el-form-item label="备注" prop="remark">
          <el-input type="textarea" :rows="4" placeholder="请输入内容" v-model="ruleForm.memo" maxlength="255" show-word-limit></el-input>
        </el-form-item>

        <el-form-item>
          <el-button v-if="$route.params.type !== 'edit'" type="primary" @click="saveForm('ruleForm')">下一步</el-button>
          <el-button v-if="$route.params.type === 'edit'" type="primary" @click="saveForm('ruleForm')">确定</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {},
      rules: {
        name: [{ required: true, message: '请输入产品名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入产品编码', trigger: 'blur' }]
      }
    }
  },

  methods: {
    addVuTime() {
      this.ruleForm.uvTime.push({ start: '', end: '' })
    },
    del(index) {
      this.ruleForm.uvTime.splice(index, 1)
    },
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          params.uvTime = JSON.stringify(params.uvTime)
          console.log(params)
          if (this.type == 'add') {
            this.httpPost('/product/add', params)
              .then(res => {
                this.ruleForm.productId = res.productId
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/blender',
                  name: 'Blender',
                  params: { type: 'add', productId: res.productId, info: this.ruleForm }
                })
              })
              .catch(() => {
                console.log('cancel')
                return false
              })
          } else if (this.type == 'pre') {
            this.httpPost('/product/mod/' + params.productId, params)
              .then(res => {
                this.$message.success('设置成功')
                this.$router.push({
                  path: '/blender',
                  name: 'Blender',
                  params: { type: 'add', productId: params.productId, info: this.ruleForm }
                })
              })
              .catch(() => {
                console.log('cancel')
                return false
              })
          } else {
            this.httpPost('/product/mod/' + params.id, params)
              .then(res => {
                this.$message.success('设置成功'),
                  this.$router.push({
                    path: '/product'
                  })
              })
              .catch(() => {
                console.log('cancel')
                return false
              })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 返回
    cancelForm() {
      this.$router.push({
        path: '/product'
      })
    }
  },

  mounted() {
    this.ruleForm = this.$route.params.info
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      this.type = 'edit'
      this.ruleForm.uvTime =JSON.parse(this.ruleForm.uvTime)
    } else if (this.$route.params.type === 'pre') {
      this.ruleForm.uvTime =JSON.parse(this.ruleForm.uvTime)
      this.type = 'pre'
    } else {
      this.$router.push({
        path: '/product'
      })
      return
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  & > div {
    width: 240px;
  }
}
.hidden {
  visibility: hidden;
}
.row_item {
  /deep/.el-date-editor {
    width: 120px !important;
  }
}
/deep/.el-picker-panel {
  width: 120px !important;
}
</style>
