<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>商品设置</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增商品</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" row-key="id">
          <el-table-column label="缩略图">
            <template slot-scope="scope">
              <img width="60" :src="imgPath(scope.row.picture)" alt="" />
            </template>
          </el-table-column>
          <el-table-column prop="name" label="商品名称（中）"> </el-table-column>
          <el-table-column prop="englishName" label="商品名称（英）"> </el-table-column>
          <el-table-column prop="sellPrice" label="商品零售价"> </el-table-column>
          <el-table-column prop="remark" label="商品备注"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="120">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link v-if="scope.row.state === 1" :underline="false" type="danger" @click.stop="disable(scope.row.id)">停用</el-link>
              <el-link v-if="scope.row.state === 0" :underline="false" type="success" @click.stop="enable(scope.row.id)">启用</el-link>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="fn_btn">
        <el-button v-if="$route.params.type === 'add'" type="primary" @click="cancelForm">上一步</el-button>
        <el-button v-if="$route.params.type === 'add'" type="primary" @click="toProduct">完成</el-button>
        <el-button v-if="$route.params.type === 'edit'" type="primary" @click="toProduct">返回</el-button>
      </div>

      <!-- 增加/修改搅拌器 -->
      <el-dialog :title="title" :visible.sync="dialogVisible" width="80%">
        <el-divider></el-divider>
        <el-form ref="ruleForm" :rules="rules" :model="ruleForm" label-width="60px">
          <el-form-item label="商品" required class="itemName">
            <el-autocomplete class="inline-input" v-model="ruleForm.itemName" :fetch-suggestions="querySearch" @blur="mustSel" placeholder="请输入后选择商品名称" @change="handleChange" @select="handleSelect" clearable></el-autocomplete>
          </el-form-item>
          <el-form-item class="stock_cont" label="料仓名">
            <div class="row_item" v-for="(item, index) in ruleForm.processList">
              <span style="margin-right: 10px">顺序：{{ index + 1 }}</span>
              <el-select v-model="item.storageId" placeholder="请选择">
                <el-option v-for="option in options" :key="option.id" :label="option.name" :value="option.id"> </el-option>
              </el-select>
              <el-form-item label="名称" prop="name" class="name">
                <el-input size="small" v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="延时(s)" prop="delay">
                <el-input size="small" @input="item.delay = item.delay.replace(/[^\d.]/g, '')" v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="水量(s)" prop="waterOut">
                <el-input size="small" @input="item.waterOut = item.waterOut.replace(/[^\d.]/g, '')" v-model="item.waterOut"></el-input>
              </el-form-item>
              <el-form-item label="下料(s)" prop="stuffOut">
                <el-input size="small" @input="item.stuffOut = item.stuffOut.replace(/[^\d.]/g, '')" v-model="item.stuffOut"></el-input>
              </el-form-item>
              <el-form-item label="搅拌(s)" prop="blender">
                <el-input size="small" @input="item.blender = item.blender.replace(/[^\d.]/g, '')" v-model="item.blender"></el-input>
              </el-form-item>
              <el-link :class="ruleForm.processList.length === 1 ? 'hidden' : ''" style="margin-left: 10px" :underline="false" type="primary" @click.stop="del(index)">删除</el-link>
              <div :class="['move_btn', ruleForm.processList.length === 1 ? 'hidden' : '']">
                <i :disabled="index === 0" @click="$toTop(index, ruleForm.processList)"><img src="@/assets/images/top.png"/></i>
                <i :disabled="index === 0" @click="$toPrev(index, ruleForm.processList)"><img src="@/assets/images/prev.png"/></i>
                <i :disabled="index === ruleForm.processList.length - 1" @click="$toNext(index, ruleForm.processList)"><img src="@/assets/images/next.png"/></i>
                <i :disabled="index === ruleForm.processList.length - 1" @click="$toBottom(index, ruleForm.processList)"><img src="@/assets/images/bottom.png"/></i>
              </div>
            </div>
            <div>
              <el-link :underline="false" type="primary" @click.stop="addStorage">新增料仓</el-link>
            </div>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="resetForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="editItem('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        productId: '',
        itemId: '',
        itemName: '',
        processList: [{ delay: 0, waterOut: 0, stuffOut: 0, blender: 0, storageId: '', sequence: '', name: '' }]
      },
      options: [],
      tableData: [],
      restaurants: [],
      dialogVisible: false,
      formLabelWidth: '100px',
      title: '',
      rules: {
        // itemName: [{ required: true, message: '请选择商品', trigger: 'change' }]
      }
    }
  },
  methods: {
    mustSel() {
      if (!this.ruleForm.itemId) this.ruleForm.itemName = null
    },
    disable(id) {
      this.$confirm('是否确认停用当前饮品？', '提醒', { type: 'warning' })
        .then(() => {
          this.httpPost('/product/item/disable/' + id, '').then(res => {
            this.$message({ type: 'success', message: '停用成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },
    enable(id) {
      this.$confirm('是否确认启用当前饮品？', '提醒', { type: 'warning' })
        .then(() => {
          this.httpPost('/product/item/enable/' + id, '').then(res => {
            this.$message({ type: 'success', message: '启用成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },
    // 删除料仓
    del(index) {
      this.ruleForm.processList.splice(index, 1)
    },
    // 新增料仓
    addStorage() {
      this.ruleForm.processList.push({ delay: 0, waterOut: 0, stuffOut: 0, blender: 0, storageId: '', value: '1', sequence: '', name: '' })
    },
    handleSelect(item) {
      console.log(item)
      // this.groupSelect = item
      // this.deviceCategoryId = item.id
      this.ruleForm.itemId = item.id
      this.ruleForm.itemName = item.name
    },
    handleChange(e) {
      console.log(e)
    },

    //输入联想
    querySearch(queryString, cb) {
      var queryForm = {
        size: 10,
        page: 1,
        total: '',
        name: queryString,
        state: 1
      }
      this.httpGet('/item/query', queryForm, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },

    imgPath(mainPic) {
      if (!mainPic) return ''
      return this.$picDownload + mainPic.substring(2, mainPic.length - 2)
    },
    cancelForm() {
      this.$router.push({
        path: '/storage',
        name: 'Storage',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    toProduct() {
      this.$router.push({
        path: '/product'
      })
    },
    toProductGood() {
      this.$router.push({
        path: '/productGood',
        name: 'ProductGood',
        params: { type: 'add', info: this.$route.params.info }
      })
    },
    loadData() {
      this.httpPost('/product/item/query/' + this.ruleForm.productId, '').then(res => {
        this.tableData = res
      })
    },
    storageQuery() {
      this.httpGet('/product/storage/query/' + this.ruleForm.productId, '').then(res => {
        this.options = res
      })
    },
    add() {
      this.title = '新增商品'
      this.dialogVisible = true
      this.ruleForm = {
        itemId: '',
        itemName: '',
        productId: this.$route.params.productId,
        processList: [{ delay: 0, waterOut: 0, stuffOut: 0, blender: 0, sequence: '', storageId: '', name: '' }]
      }
    },

    edit(info) {
      this.title = '修改商品'
      this.httpGet('/product/item/' + info.id, '').then(ret => {
        this.ruleForm = {
          productId: ret.productId,
          itemId: ret.itemId,
          itemName: ret.name,
          processList: ret.processList
        }
      })
      this.dialogVisible = true
    },

    editItem(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          for (var i = 0; i < this.ruleForm.processList.length; i++) {
            this.ruleForm.processList[i].sequence = i + 1
          }
          if (!this.ruleForm.itemName) return this.$message.warning('请输入后选择商品名称'), false
          if (this.title == '新增商品') {
            console.log(this.ruleForm.processList)
            this.httpPost('/product/item/edit', this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '增加成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          } else {
            this.httpPost('/product/item/edit', this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '修改成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    resetForm(formName) {
      this.dialogVisible = false
    }
  },

  mounted() {
    this.ruleForm = {
      productId: '',
      id: '',
      code: '',
      name: '',
      memo: '',
      blenderId: '',
      speed: '',
      alarm: '',
      full: ''
    }
    this.ruleForm.productId = this.$route.params.productId
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      this.type = 'edit'
    } else {
      this.$router.push({
        path: '/product'
      })
      return
    }
    this.storageQuery()
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.itemName {
  /deep/.el-form-item__content {
    display: flex;
    align-items: center;
  }
  /deep/.el-form-item__error {
    position: static;
  }
}
.row_item {
  display: flex;
  align-items: center;
  .el-select {
    width: 180px;
  }
  .name {
    /deep/.el-form-item__content {
      width: 160px;
    }
  }
  /deep/.el-form-item__label {
    width: 55px !important;
    padding: 0;
  }
  /deep/.el-form-item__content {
    width: 50px;
    margin-left: 60px !important;
  }
}
.hidden {
  visibility: hidden;
}
</style>
