<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/role' }">安卓升级</el-breadcrumb-item>
        <el-breadcrumb-item>新增Apk</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="apk文件" prop="filename" ref="apkName">
          <el-upload class="upload-demo" accept=".apk" :limit="1" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeUpload" :on-remove="() => (ruleForm.filename = '')">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 上传</div>
          </el-upload>
        </el-form-item>
        <el-form-item label="产品型号" prop="productName">
          <el-autocomplete class="inline-input" v-model="ruleForm.productName" @blur="mustSel" :fetch-suggestions="querySearch" placeholder="请输入选择型号" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item>
        <el-form-item label="版本名称" prop="versionName">
          <el-input size="small" v-model="ruleForm.versionName" placeholder="请输入版本名称"></el-input>
        </el-form-item>
        <el-form-item label="版本编号" prop="versionCode">
          <el-input type="number" size="small" v-model.number="ruleForm.versionCode" placeholder="请输入版本编号"></el-input>
        </el-form-item>
        <el-form-item label="版本描述" prop="memo">
          <el-input size="small" v-model="ruleForm.memo" placeholder="请输入版本描述"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
// import menuList from '@/utils/menu'
export default {
  data() {
    return {
      ruleForm: {
        filename: '',
        memo: '',
        productId: '', // 必选
        versionCode: '',
        versionName: '',
        productName: ''
      },
      rules: {
        productName: [{ required: true, message: '请输入选择型号', trigger: 'change' }],
        versionName: [{ required: true, message: '请输入版本名称', trigger: 'change' }],
        versionCode: [{ required: true, message: '请输入版本编号', trigger: 'change' }],
        filename: [{ required: true, message: '请上传Apk文件', trigger: 'change' }]
      },
      // 联想
      restaurants: [],
      isSelect: false,
      productId: '',
      productName: ''
    }
  },

  methods: {
    // 文件上传

    uploadAction() {
      return this.$picUpload
      // return ''
    },
    uploadSuccess(res, file, fileList) {
      console.log(res, file, fileList)
      this.ruleForm.filename = res.data.fileName
      let el = this.$refs.apkName.$el.children[1].children[1] // 上传文件后取消提示
      if (el) el.style.display = 'none'
    },
    beforeUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 500
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 500MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 文件上传
    handleSelect(item) {
      console.log(item)
      this.productId = item.id
      this.isSelect = true // 是否选择了数据
    },
    // 设备型号联想查询
    querySearch(queryString, cb) {
      var params = { /* size: 10, page: 1, */ total: 1, state: 1, name: queryString }
      this.httpGet('/product/query/', params, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })

      // var results = queryString ? this.restaurants.filter(this.createFilter(queryString)) : this.restaurants
      // // 调用 callback 返回建议列表的数据
      // cb(results)
    },
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log()
        if (valid) {
          if (!this.isSelect && this.ruleForm.productId !== '') {
            this.ruleForm.productId = ''
            return this.$message.warning('请输入后选择正确产品')
          }
          let params = Object.assign({}, this.ruleForm)
          params.productId = this.productId
          console.log(params)
          this.httpPost('/apk/add', params).then(res => {
            this.$message.success('新增成功')
            this.$router.back()
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 返回
    cancelForm() {
      this.$router.back()
    },
    mustSel() {
      this.$mustSel(this, this.ruleForm.productId, 'productName')
    }
  },

  mounted() {}
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
}
</style>
