<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>产品管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="使用中" name="selling">
          <ProductTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @edit="edit" @editBlender="editBlender" @editStorage="editStorage" @editGood="editGood" @del="del" />
        </el-tab-pane>

        <el-tab-pane label="已停用" name="disuse">
          <ProductTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @edit="edit" @enable="enable" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import ProductTable from '@/views/base/product/ProductTable'
export default {
  components: { ProductTable },
  data() {
    return {
      ruleForm: {
        productId: '',
        name: '',
        code: '',
        price: '',
        memo: '',
        uvTime:[{ start: '', end: ''}]
      },
      activeName: 'selling',
      queryForm: {
        name: '',
        code: '',
        state: 1, //1:使用中，0:已停用
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      editInfo: {},
      dialogVisible: false,
      formLabelWidth: '120px'
    }
  },
  methods: {
    getPage(page) {
      this.queryForm.page = page
      this.loadData()
    },
    getSearch(params) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editproduct', name: 'EditProduct', params: { type: 'add', info: this.ruleForm } })
    },

    // 修改基本信息
    edit(info) {
      this.$router.push({ path: '/editproduct', name: 'EditProduct', params: { type: 'edit', info } })
    },

    // 修改搅拌机信息
    editBlender(info) {
      this.$router.push({ path: '/blender', name: 'Blender', params: { type: 'edit', productId: info.id, info: info } })
    },

    // 修改料仓信息
    editStorage(info) {
      this.$router.push({ path: '/storage', name: 'Storage', params: { type: 'edit', productId: info.id, info: info } })
    },

    // 修改商品信息
    editGood(info) {
      this.$router.push({ path: '/productGood', name: 'ProductGood', params: { type: 'edit', productId: info.id } })
    },

    del(id) {
      this.$confirm('此操作将停用数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/product/disable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '停用成功!' })
            this.handleClick()
          })
        })
        .catch(() => {})
    },
    enable(id) {
      this.$confirm('此操作将启用数据1, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/product/enable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '启用成功!' })
            this.handleClick()
          })
        })
        .catch(() => {})
    },

    handleClick(tab, event) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    loadData() {
      let params = Object.assign({}, this.queryForm)
      params.state = this.activeName === 'selling' ? 1 : 0
      this.httpGet('/product/query/', params).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
  /deep/.el-form-item__content {
    & > div {
      width: 240px !important;
    }
  }
</style>
