<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>搅拌机设置</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!--<el-form ref="queryForm" :model="queryForm" label-width="100px" class="form_border">-->
      <!--<el-form-item label="编号" prop="code">-->
      <!--<el-input v-model="queryForm.code" placeholder="请输入搅拌器编号"></el-input>-->
      <!--</el-form-item>-->
      <!--<el-form-item label="名称" prop="name">-->
      <!--<el-input v-model="queryForm.name" placeholder="请输入搅拌器名称"></el-input>-->
      <!--</el-form-item>-->
      <!--<el-form-item class="select_btn">-->
      <!--<el-button type="primary" @click="loadData">搜索</el-button>-->
      <!--</el-form-item>-->
      <!--</el-form>-->

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增搅拌器</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" row-key="id">
          <el-table-column prop="code" label="搅拌器编号"> </el-table-column>
          <el-table-column prop="name" label="搅拌器名称"> </el-table-column>
          <el-table-column prop="memo" label="备注"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <!--<el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>-->
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="fn_btn">
        <el-button v-if="$route.params.type === 'add'" type="primary" @click="cancelForm">上一步</el-button>
        <el-button v-if="$route.params.type === 'add'" type="primary" @click="toStorage">下一步</el-button>
        <el-button v-if="$route.params.type === 'edit'" type="primary" @click="toProduct">返回</el-button>
      </div>

      <!-- 增加/修改搅拌器 -->
      <el-dialog :title="title" :visible.sync="dialogVisible" width="35%">
        <el-divider></el-divider>
        <el-form ref="ruleForm" :rules="blenderRules" :model="ruleForm" label-width="100px">
          <el-form-item label="搅拌器编号" prop="code">
            <el-input disabled size="small" v-model="ruleForm.code"></el-input>
          </el-form-item>
          <el-form-item label="搅拌器名称" prop="name">
            <el-input size="small" v-model="ruleForm.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="备注" prop="memo">
            <el-input :rows="4" type="textarea" placeholder="请输入内容" v-model="ruleForm.memo" maxlength="255"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="resetForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="editBlender('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        productId: '',
        id: '',
        code: '',
        name: '',
        memo: ''
      },
      tableData: [],
      dialogVisible: false,
      formLabelWidth: '100px',
      title: '',
      blenderRules: {
        name: [{ required: true, message: '请输入搅拌器名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入搅拌器编码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    cancelForm() {
      this.$router.push({
        path: '/editproduct',
        name: 'EditProduct',
        params: { type: 'pre', info: this.$route.params.info }
      })
    },
    toProduct() {
      this.$router.push({
        path: '/product'
      })
    },
    toStorage() {
      this.$router.push({
        path: '/storage',
        name: 'Storage',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    loadData() {
      this.httpGet('/product/blender/query/' + this.ruleForm.productId, '').then(res => {
        this.tableData = res
      })
    },
    add() {
      this.title = '新增搅拌器'
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm = {
          id: '',
          productId: this.$route.params.productId,
          code: this.$route.params.info.code.concat(this.$formatCode(this.tableData.length)),
          name: '',
          memo: ''
        }
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    },

    edit(info) {
      this.title = '修改搅拌器'
      this.ruleForm = Object.assign({}, info)
      this.dialogVisible = true
    },

    editBlender(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.title == '新增搅拌器') {
            this.httpPost('/product/blender/add', this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '增加成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          } else {
            this.httpPost('/product/blender/mod/' + this.ruleForm.id, this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '修改成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },

  mounted() {
    this.ruleForm = {
      productId: this.$route.params.productId,
      id: '',
      code: '',
      name: '',
      memo: ''
    }
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      this.type = 'edit'
    } else {
      this.$router.push({
        path: '/product'
      })
      return
    }
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  /deep/.el-form-item__content {
    & > div {
      width: 260px !important;
    }
  }
}
</style>
