<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>料仓设置</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增料仓</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" row-key="id">
          <el-table-column prop="code" label="料仓编号"> </el-table-column>
          <el-table-column prop="name" label="料仓名称"> </el-table-column>
          <el-table-column prop="blenderName" label="搅拌器名称"> </el-table-column>
          <el-table-column prop="speed" label="下料速度"> </el-table-column>
          <el-table-column prop="alarm" label="警报值"> </el-table-column>
          <el-table-column prop="stop" label="停售值"> </el-table-column>
          <el-table-column prop="full" label="满量值"> </el-table-column>
          <el-table-column prop="specialMaterial" label="特殊材料">
            <template slot-scope="scope">
              <el-tag type="danger" v-if="scope.row.specialMaterial === 0">否</el-tag>
              <el-tag v-if="scope.row.specialMaterial === 1">糖</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="memo" label="备注"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <!--<el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>-->
            </template>
          </el-table-column>
        </el-table>
        <div class="fn_btn">
          <el-button v-if="$route.params.type === 'add'" type="primary" @click="cancelForm">上一步</el-button>
          <el-button v-if="$route.params.type === 'add'" type="primary" @click="toProductGood">下一步</el-button>
          <el-button v-if="$route.params.type === 'edit'" type="primary" @click="toProduct">返回</el-button>
        </div>
      </div>

      <!-- 增加/修改搅拌器 -->
      <el-dialog :title="title" :visible.sync="dialogVisible" width="80%">
        <el-divider></el-divider>
        <el-form inline ref="ruleForm" :rules="rules" :model="ruleForm" label-width="120px">
          <el-form-item label="料仓编号" prop="code">
            <el-input disabled size="small" v-model="ruleForm.code"></el-input>
          </el-form-item>
          <el-form-item label="料仓名称" prop="name">
            <el-input size="small" v-model="ruleForm.name" clearable></el-input>
          </el-form-item>
          <el-form-item label="搅拌机" prop="blenderId">
            <el-select size="small" v-model="ruleForm.blenderId" placeholder="选择搅拌器">
              <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="下料速度(g/s)" prop="speed">
            <el-input size="small" v-model="ruleForm.speed" clearable></el-input>
          </el-form-item>
          <el-form-item label="满量值(g)" prop="full">
            <el-input size="small" v-model="ruleForm.full" clearable></el-input>
          </el-form-item>
          <el-form-item label="警报值(g/s)" prop="alarm">
            <el-input size="small" v-model="ruleForm.alarm" clearable></el-input>
          </el-form-item>
          <el-form-item label="停售值(g/s)" prop="stop">
            <el-input size="small" v-model="ruleForm.stop" clearable></el-input>
          </el-form-item>
          <el-form-item label="特殊材料" prop="specialMaterial">
            <el-radio-group v-model="ruleForm.specialMaterial">
              <el-radio :label="1">糖</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="备注" prop="memo">
            <el-input style="width: 120%" :rows="2" type="textarea" placeholder="请输入内容" v-model="ruleForm.memo" maxlength="255"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="resetForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="editBlender('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        productId: '',
        id: '',
        code: '',
        name: '',
        memo: '',
        blenderId: '',
        speed: '',
        alarm: '',
        full: '',
        stop:'',
        specialMaterial: 0
      },
      options: [],
      tableData: [],
      dialogVisible: false,
      formLabelWidth: '100px',
      title: '',
      rules: {
        name: [{ required: true, message: '请输入料仓名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入料仓编码', trigger: 'blur' }],
        blenderId: [{ required: true, message: '请选择搅拌器', trigger: 'chanage' }],
        speed: [{ required: true, message: '请输入下料速度', trigger: 'blur' }],
        alarm: [{ required: true, message: '请输入警报值', trigger: 'blur' }],
        full: [{ required: true, message: '请输入满量值', trigger: 'blur' }],
        stop: [{ required: true, message: '请输入停售值', trigger: 'blur' }],
        specialMaterial: [{ required: true, message: '请选择特殊材料值', trigger: 'change' }]
      }
    }
  },
  methods: {
    cancelForm() {
      this.$router.push({
        path: '/blender',
        name: 'Blender',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    toProduct() {
      this.$router.push({
        path: '/product'
      })
    },
    toProductGood() {
      this.$router.push({
        path: '/productGood',
        name: 'ProductGood',
        params: { type: 'add', productId: this.ruleForm.productId, info: this.$route.params.info }
      })
    },
    loadData() {
      this.httpGet('/product/storage/query/' + this.ruleForm.productId, '').then(res => {
        this.tableData = res
      })
    },
    blenderQuery() {
      this.httpGet('/product/blender/query/' + this.ruleForm.productId, '').then(res => {
        this.options = res
      })
    },
    add() {
      this.title = '新增料仓'
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm = {
          productId: this.$route.params.productId,
          code: this.$route.params.info.code.concat(this.$formatCode(this.tableData.length)),
          id: '',
          name: '',
          memo: '',
          blenderId: '',
          speed: '',
          alarm: '',
          full: '',
          specialMaterial: 0
        }
      })
    },

    edit(info) {
      this.title = '修改料仓'
      this.ruleForm = Object.assign({}, info)
      this.dialogVisible = true
    },

    editBlender(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.title == '新增料仓') {
            this.httpPost('/product/storage/add', this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '增加成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          } else {
            this.httpPost('/product/storage/mod/' + this.ruleForm.id, this.ruleForm).then(ret => {
              this.$message({ type: 'success', message: '修改成功!' })
              this.dialogVisible = false
              this.loadData()
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    }
  },

  mounted() {
    this.ruleForm.productId = this.$route.params.productId
    if (this.$route.params.type === 'add') {
      this.type = 'add'
    } else if (this.$route.params.type === 'edit') {
      this.type = 'edit'
    } else {
      this.$router.push({
        path: '/product'
      })
      return
    }
    this.blenderQuery()
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  & > div {
    width: 240px !important;
  }
}
</style>
