<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/company' }">公司管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改公司' : '新增公司' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form inline :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
        <el-form-item label="公司名称" prop="name">
          <el-input size="small" v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="内部企业编号" prop="code">
          <el-input size="small" v-model="ruleForm.code"></el-input>
        </el-form-item>
        <el-form-item label="统一社会信用代码" prop="creditCode">
          <el-input size="small" v-model="ruleForm.creditCode"></el-input>
        </el-form-item>
        <el-form-item label="公司电话" prop="phone">
          <el-input size="small" v-model="ruleForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="公司地址" prop="address">
          <el-input size="small" v-model="ruleForm.address"></el-input>
        </el-form-item>
        <!--<el-form-item label="传真电话" prop="fax">-->
        <!--<el-input size="small" v-model="ruleForm.fax"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item label="法人/代理人" prop="legalPerson">
          <el-input size="small" v-model="ruleForm.legalPerson"></el-input>
        </el-form-item>
        <!--<el-form-item label="性别" prop="legalGender">-->
        <!--<el-select v-model="ruleForm.legalGender" placeholder="请选择">-->
        <!--<el-option v-for="item in genderOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>-->
        <!--</el-select>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="法人证件类型" prop="legalCertType">-->
        <!--<el-select v-model="ruleForm.legalCertType" placeholder="请选择">-->
        <!--<el-option v-for="item in certTypeOptions" :key="item.value" :label="item.label" :value="item.value"> </el-option>-->
        <!--</el-select>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="法人证件号码" prop="legalCertCode">-->
        <!--<el-input size="small" v-model="ruleForm.legalCertCode"></el-input>-->
        <!--</el-form-item>-->
        <!--<el-form-item label="法人所属民族" prop="legalNation">-->
        <!--<el-input size="small" v-model="ruleForm.legalNation"></el-input>-->
        <!--</el-form-item>-->
        <hr v-if="$route.params.type === 'add'" size="1" color="#5151A2" style="margin:20px 0px 20px 0px " />
        <el-form-item v-if="$route.params.type === 'add'" label="管理员账号" prop="account">
          <el-input size="small" v-model="ruleForm.account"></el-input>
        </el-form-item>
        <el-form-item v-if="$route.params.type === 'add'" label="密码" prop="password">
          <el-input type="password" size="small" v-model="ruleForm.password"></el-input>
        </el-form-item>
        <hr size="1" color="#5151A2" style="margin:20px 0px 20px 0px " />
        <el-form-item label="资源列表" prop="resourceList">
          <el-tree class="my_tree" ref="tree" :data="rightList" show-checkbox node-key="id" @check="checkChange" :default-checked-keys="checkList" :props="defaultProps" default-expand-all check-on-click-node> </el-tree>
        </el-form-item>

        <el-form-item label=" ">
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        id: '',
        name: '',
        code: '',
        creditCode: '',
        phone: '',
        address: '',
        fax: '',
        legalPerson: '',
        legalCertType: '',
        legalCertCode: '',
        legalGender: '',
        legalNation: '',
        resourceList: [],
        account: '',
        password: ''
      },
      rules: {
        name: [{ required: true, message: '请输入公司名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入内部企业编号', trigger: 'blur' }],
        creditCode: [{ required: true, message: '请输入统一社会信用代码', trigger: 'blur' }],
        account: [{ required: true, message: '请输入管理员账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入管理员密码', trigger: 'blur' }]
      },
      uploadImgMain: false,
      certTypeOptions: [
        { value: '1', label: '身份证' },
        { value: '2', label: '护照' },
        { value: '3', label: '台湾居民来往大陆通行证' },
        { value: '4', label: '港澳居民来往内地通行证' },
        { value: '5', label: '外国人永久居留证' }
      ],
      genderOptions: [
        { value: '0', label: '未知的性别' },
        { value: '1', label: '男性' },
        { value: '2', label: '女性' },
        { value: '5', label: '女性改（变）为男性' },
        { value: '6', label: '男性改（变）为女性' },
        { value: '9', label: '未说明的性别' }
      ],
      // 权限
      sourceForm: {},
      defaultProps: {
        children: 'childNode',
        label: 'name'
      },
      rightList: [],
      checkList: [], // 得到的数据
      checkListAll: '' //  已改变的数据带有一级
    }
  },

  methods: {
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          console.log(this.checkList)
          this.ruleForm.resourceList = this.checkListAll
          const params = Object.assign({}, this.ruleForm)
          console.log(params)
          if (this.type === 'add') {
            this.httpPost('/basic/enterprise/add', params).then(res => {
              this.$message.success('新增成功')
              this.$router.back()
            })
          } else {
            this.httpPost('/basic/enterprise/mod/' + params.id, params).then(res => {
              this.$message.success('修改成功')
              this.$router.back()
            })
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    chooseResource() {
      this.$router.push({ path: '/editResource', name: 'EditResource', params: { type: 'add', info: this.ruleForm } })
    },
    // 返回
    cancelForm() {
      this.$router.push({ path: '/company', name: 'Company', params: {} })
    },

    // 权限
    // 权限选择变化回调监听
    checkChange(currNode, checkedObj) {
      // console.log(checkedObj.checkedKeys)
      this.checkList = checkedObj.checkedKeys
      this.checkListAll = this.$roleJoin(checkedObj.checkedNodes)
    }
  },

  mounted() {
    if (!this.$route.params.type) return this.$router.back()
    if (this.$route.params.type === 'edit') {
      this.type = 'edit'
      this.ruleForm = this.$route.params.info
      // this.ruleForm.legalGender =this.ruleForm.legalGender?this.ruleForm.legalGender.toString():''
    } else {
      this.type = 'add'
    }

    // 权限
    const params = {
      state: 1, // 1:使用中，0:禁用中
      size: 999,
      page: 1,
      total: ''
    }
    this.httpPost('/basic/resource/query', params).then(res => {
      var menuList = []
      for (var i = 0; i < res.rows.length; i++) {
        if (res.rows[i].parentId == 0) {
          res.rows[i].childNode = []
          menuList.push(res.rows[i])
        } else {
          for (var j = 0; j < menuList.length; j++) {
            if (menuList[j].id === res.rows[i].parentId) {
              menuList[j].childNode.push(res.rows[i])
              break
            }
          }
        }
      }
      console.log(menuList)
      const menu = Object.assign([], menuList)
      this.menuArr = Object.assign([], menuList)
      this.rightList = menu
      if (this.$route.params.type === 'edit') {
        this.httpPost('/basic/enterprise/query/resource/' + this.ruleForm.id, '').then(ret => {
          this.checkList = ret
          this.sourceForm.resourceList = ret
        })
      } else {
        for (var i = 0; i < res.rows.length; i++) {
          if (res.rows[i].defaultAssign == 1) {
            this.checkList.push(res.rows[i].id)
          }
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 260px !important;
  }
  .my_tree {
    width: auto !important;
    display: flex;
    padding-top: 8px;
    .el-tree-node {
      margin-right: 10px;
    }
    /deep/.el-tree__empty-block {
      min-width: 200px;
    }
  }
}
</style>
