<template>
  <div>
    <el-form ref="queryInfo" :model="queryInfo" label-width="120px" class="form_border">
      <el-form-item label="员工姓名" prop="name">
        <el-input v-model="queryInfo.name" placeholder="请输入员工姓名" clearable></el-input>
      </el-form-item>
      <el-form-item label="员工编号" prop="code">
        <el-input v-model="queryInfo.code" placeholder="请输入员工编号" clearable></el-input>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <el-button @click.stop="add" icon="el-icon-plus">新增员工</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="account" label="员工账号"> </el-table-column>
        <el-table-column prop="name" label="员工姓名"> </el-table-column>
        <el-table-column prop="code" label="员工编号"> </el-table-column>
        <el-table-column prop="enterpriseName" label="所属企业"> </el-table-column>
        <el-table-column prop="mobile" label="手机号码"> </el-table-column>
        <el-table-column prop="openid" label="公众号">
          <template slot-scope="scope">
            <el-link v-if="scope.row.openid" type="success" :underline="false">已关注</el-link>
            <el-link v-else type="danger" :underline="false">未关注</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="memo" label="备注"> </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="160">
          <template slot-scope="scope">
            <el-link v-if="scope.row.state == 1" :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            <!--<el-link v-if="scope.row.state == 1" :underline="false" type="primary" @click.stop="editPassDia(scope.row)">修改密码</el-link>-->
            <el-link v-if="scope.row.state == 1" :underline="false" type="primary" @click.stop="resetPassDia(scope.row)">重置密码</el-link>
            <el-link v-if="scope.row.state == 1" :underline="false" type="danger" @click.stop="disable(scope.row.id)">停用</el-link>
            <el-link v-if="scope.row.state == 0" :underline="false" type="primary" @click.stop="enable(scope.row.id)">启用</el-link>
            <el-link v-if="scope.row.state == 1" :underline="false" type="primary" @click.stop="transfer(scope.row.id)">设备转移</el-link>

          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
      <!-- 修改密码对话框 -->
      <el-dialog title="修改密码" :visible.sync="editPassDialog">
        <div class="modal-cont">
          <el-form :model="modifyForm" :rules="changeFormRules" ref="modifyForm" label-width="100px">
            <el-form-item prop="oldPassword" label="原密码">
              <el-input prefix-icon="el-icon-user-solid" type="password" style="width: 100%" v-model="modifyForm.oldPassword" placeholder="原密码"></el-input>
            </el-form-item>
            <el-form-item prop="newPassword" label="新密码">
              <el-input type="password" prefix-icon="el-icon-shopping-bag-2" style="width: 100%;" v-model="modifyForm.newPassword" placeholder="新密码"></el-input>
            </el-form-item>
            <el-form-item prop="reNewPassword" label="确认密码">
              <el-input type="password" prefix-icon="el-icon-shopping-bag-2" style="width: 100%;" v-model="modifyForm.reNewPassword" placeholder="确认密码"></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click.stop="modPwd()">确 定</el-button>
        </span>
      </el-dialog>
      <!-- 重置密码对话框 -->
      <el-dialog title="重置密码" :visible.sync="resetPassDialog">
        <div class="modal-cont">
          <el-form :model="resetForm" :rules="rules" ref="resetForm" label-width="100px">
            <el-form-item prop="newPassword" label="新密码">
              <el-input type="password" prefix-icon="el-icon-shopping-bag-2" style="width: 100%;" v-model="resetForm.newPassword" placeholder="新密码" show-password></el-input>
            </el-form-item>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click.stop="resetPwd()">确 定</el-button>
        </span>
      </el-dialog>

      <!--一键转移设备 -->
      <el-dialog title="一键转移设备" :visible.sync="transferDialogVisible" width="25%">
        <el-divider></el-divider>

        <el-form ref="queryForm" :model="queryForm" label-width="100px">
          <el-form-item label="选择被移交人"  prop="toUserId">
            <el-autocomplete class="inline-input" v-model="toUserName" :fetch-suggestions="queryEmployee" placeholder="请输入负责人名称" @blur="mustSelEmployee" @select="employeeHandleSelect" clearable></el-autocomplete>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="transferDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="transferDevice">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'queryForm'],
  data() {
    // 密码验证
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.modifyForm.reNewPassword !== '') {
          this.$refs.modifyForm.validateField('reNewPassword')
        }
        callback()
      }
    }
    // 确认密码
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.modifyForm.newPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      selectId: '',
      resetForm: {
        newPassword: '888888'
      },
      queryInfo: this.queryForm,
      options: [],
      enterpriseName: '',
      restaurants: [],
      modifyForm: {
        oldPassword: '',
        newPassword: '',
        reNewPassword: ''
      },
      editPassDialog: false,
      resetPassDialog: false,
      rules: {
        newPassword: [{ required: true, message: '请输入新密码', trigger: 'blur' }]
      },
      changeFormRules: {
        oldPassword: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
        newPassword: [{ validator: validatePass, trigger: 'blur' }],
        reNewPassword: [{ validator: validatePass2, trigger: 'blur' }]
      },
      transferDialogVisible:false,
      toUserId:'',
      toUserName:''
    }
  },
  methods: {
    //一键移交设备
    transferDevice(){
      if(!this.toUserId)
        return this.$message.error('请选择被移交人')
      var params = {
        fromUserId: this.selectId,
        toUserId: this.toUserId,
      }
      this.httpPost('/device/transfer', params).then(res => {
        this.$message.success('移交成功')
        this.transferDialogVisible = false
        this.search()
      })
    },
    transfer(id){
      this.selectId = id
      this.toUserId=''
      this.toUserName=''
      this.transferDialogVisible = true
    },
    employeeHandleSelect(item) {
      console.log(item)
      this.toUserName = item.code+' - '+item.name
      this.toUserId = item.id
    },
    mustSelEmployee() {
      if (!this.toUserId) this.toUserName = null
    },
    //移交人联想查询
    queryEmployee(queryString, cb){
      var params = {
        size: 10,
        page: 1,
        total: '',
        state:1,
        name:queryString
      }
      this.httpPost('/basic/employee/query', params, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.code+' - '+i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },
    editPassDia(info) {
      this.selectId = info.id
      this.modifyForm = {
        oldPassword: '',
        newPassword: '',
        reNewPassword: ''
      }
      this.editPassDialog = true
      this.$nextTick(() => {
        this.$refs.modifyForm.resetFields()
      })
    },

    resetPassDia(info) {
      this.selectId = info.id
      // this.resetForm.newPassword = ''
      this.resetPassDialog = true
      this.$nextTick(() => {
        this.$refs.resetForm.resetFields()
      })
    },

    resetPwd() {
      this.$refs['resetForm'].validate(valid => {
        if (valid) {
          const params = {
            employeeId: this.selectId,
            newPassword: this.resetForm.newPassword
          }
          this.httpPost('/basic/employee/resetPassword', params)
            .then(res => {
              this.$message.success('重置成功')
              this.resetPassDialog = false
            })
            .catch(res => {
              return this.$message.error(res), false
            })
        } else {
          return false
        }
      })
    },

    modPwd() {
      this.$refs['modifyForm'].validate(valid => {
        if (valid) {
          const params = {
            oldPassword: this.modifyForm.oldPassword,
            newPassword: this.modifyForm.newPassword,
            reNewPassword: this.modifyForm.reNewPassword
          }
          this.httpPost('/basic/employee/modPassword', params)
            .then(res => {
              this.$message.success('修改成功')
              this.editPassDialog = false
              this.httpPost('/basic/employee/logout', params).then(ret => {
                this.$sessionData('remove', 'USER')
                this.$router.replace('/login')
              })
            })
            .catch(res => {
              return this.$message.error(res), false
            })
        } else {
          return false
        }
      })
    },

    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },
    search() {
      this.$emit('getSearch', this.queryInfo)
    },
    add() {
      this.$emit('add')
    },
    edit(info) {
      this.$emit('edit', info)
    },
    disable(id) {
      this.$emit('disable', id)
    },
    enable(id) {
      this.$emit('enable', id)
    },
    editResource(info) {
      this.$emit('editResource', info)
    }
  },
  watch: {},
  mounted() {}
}
</script>

<style lang="scss" scoped>
.modal-cont {
  /deep/.el-form-item__content {
    & > div {
      width: 300px !important;
    }
  }
}
/deep/.el-dialog {
  width: 500px !important;
}
</style>
