<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>安卓升级</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form ref="queryForm" :model="queryForm" label-width="100px" class="form_border">
        <el-form-item label="产品型号" prop="productId">
          <el-autocomplete class="inline-input" v-model="productName" :fetch-suggestions="querySearch" placeholder="请输入选择型号" @blur="mustSel" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item>

        <el-form-item class="select_btn">
          <el-button type="primary" @click="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增Apk</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color"></div>
          </div>
        </div>
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="filename" label="apk文件"> </el-table-column>
          <el-table-column prop="memo" label="版本描述"> </el-table-column>
          <el-table-column prop="productName" label="所属产品名称"> </el-table-column>
          <el-table-column label="版本状态" width="140">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.state === 0" type="info">旧版本</el-tag>
              <el-tag v-else type="primary">新版本</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="versionCode" label="版本编号" width="140"> </el-table-column>
          <el-table-column prop="versionName" label="版本名称" width="140"> </el-table-column>
          <el-table-column prop="createDatetime" label="创建时间" width="140">
            <template slot-scope="scope">
              {{ $moment(scope.row.createDatetime).format('YYYY-MM-DD') }}
            </template>
          </el-table-column>
          <!-- 操作 -->
          <!-- <el-table-column label="操作" width="160">
          <template slot-scope="scope">
            <el-link :underline="false" type="danger" @click.stop="del(scope.row)">删除</el-link>
          </template>
        </el-table-column> -->
        </el-table>
        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        page: 1,
        size: 10,
        total: '',
        productId: '',
        state: 1, // 1 在用版本， 0老版本
        memo: ''
      },
      tableData: [],
      // 联想
      restaurants: [],
      isSelect: false,
      groupSelect: {},
      productId: '',
      productName: ''
    }
  },
  methods: {
    mustSel() {
      this.$mustSel(this, this.queryForm.productId, 'productName')
    },
    add() {
      this.$router.push('/addapk')
    },
    handleSelect(item) {
      console.log(item)
      this.groupSelect = item
      this.productId = item.id
      this.isSelect = true // 是否选择了数据
    },
    // 设备型号联想查询
    querySearch(queryString, cb) {
      var params = {
        // size: 10,
        // page: 1,
        total: 1,
        state: 1,
        name: queryString
      }
      this.httpGet('/product/query/', params, null, true).then(res => {
        res.rows.map(i => {
          i.value = i.name
        })
        this.restaurants = res.rows
        cb(this.restaurants)
      })

      // var results = queryString ? this.restaurants.filter(this.createFilter(queryString)) : this.restaurants
      // // 调用 callback 返回建议列表的数据
      // cb(results)
    },
    onSubmit() {
      console.log(this.queryForm)
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    loadData() {
      if (!this.isSelect && this.queryForm.productId !== '') {
        this.queryForm.productId = ''
        return this.$message.warning('请输入后选择正确产品')
      }
      let params = Object.assign({}, this.queryForm)
      params.productId = this.queryForm.productId ? this.productId : ''
      console.log(params)
      this.httpPost('/apk/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  /deep/.el-form-item__content {
    & > div {
      width: 240px !important;
    }
  }
}
</style>
