<template>
  <div>
    <el-form ref="queryInfo" :model="queryInfo" label-width="120px" class="form_border">
      <el-form-item label="公司名称" prop="name">
        <el-input v-model="queryInfo.name" placeholder="请输入公司名称"></el-input>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <el-button  @click.stop="add" icon="el-icon-plus">新增公司</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="name" label="公司名称"> </el-table-column>
        <el-table-column prop="code" label="内部企业编号"> </el-table-column>
        <el-table-column prop="creditCode" label="统一社会信用代码"> </el-table-column>
        <el-table-column prop="phone" label="公司电话"> </el-table-column>
        <el-table-column prop="address" label="地址"> </el-table-column>
        <el-table-column prop="legalPerson" label="法人/代理人"> </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="160">
          <template slot-scope="scope">
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            <!--<el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="editResource(scope.row)">修改权限</el-link>-->
            <el-link v-if="scope.row.state==1" :underline="false" type="danger" @click.stop="disable(scope.row.id)">停用</el-link>
            <el-link v-if="scope.row.state==0" :underline="false" type="primary" @click.stop="enable(scope.row.id)">启用</el-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['tableData', 'queryForm'],
    data() {
      return {
        queryInfo: this.queryForm
      }
    },
    methods: {
      changePage() {
        this.$emit('getPage', this.queryInfo.page)
      },
      search() {
        this.$emit('getSearch', this.queryInfo)
      },
      add() {
        this.$emit('add')
      },
      edit(info) {
        this.$emit('edit', info)
      },
      disable(id) {
        this.$emit('disable', id)
      },
      enable(id) {
        this.$emit('enable', id)
      },
      editResource(info){
        this.$emit('editResource', info)
      }
    },
    watch: {},
    mounted(){

    }
  }
</script>

<style lang="scss" scoped></style>
