<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>设备管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="getSearch">
        <!--  -->
        <el-tab-pane label="使用中" name="inOperation">
          <DeviceTable v-if="activeName === 'inOperation'" :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" />
        </el-tab-pane>

        <el-tab-pane label="停用中" name="outOperation">
          <DeviceTable v-if="activeName === 'outOperation'" :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import DeviceTable from '@/views/base/device/DeviceTable'
export default {
  components: { DeviceTable },
  data() {
    return {
      activeName: 'inOperation',
      queryForm: {
        isAll: false,
        deviceMac: '',
        productId: '',
        enable: 1, //1:使用中，0:禁用中
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      multipleSelection: [],
      chooseGoodsStr: '',
      options: [],
      // dialogVisible: false,
      // itemName:'',
      formLabelWidth: '100px',

      // 联想
      restaurants: [],
      // isSelect: false,
      groupSelect: {},
      productId: ''
      // productName:''
    }
  },
  methods: {
    // checkMachine(itemName) {
    //   this.itemName = itemName
    //   this.dialogVisible = true
    // },
    getPage(page) {
      this.queryForm.page = page
      this.loadData()
    },
    getSearch() {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },

    loadData() {
      // if (!this.isSelect && this.queryForm.productId !== '') {
      //   this.queryForm.productId = ''
      //   return this.$message.warning('请输入后选择正确产品')
      // }
      let form = Object.assign({}, this.queryForm)
      form.enable = this.activeName === 'inOperation' ? 1 : 0
      // if (this.queryForm.productId) form.productId = this.groupSelect.id
      this.httpGet('/device/query/', form).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
        // this.isSelect = false // 重置是否选择了数据
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form {
  display: flex;
  flex-wrap: wrap;
  padding-top: 10px;
  margin-bottom: 20px;
  .select_btn .el-form-item__content {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    height: 40px;
    box-sizing: border-box;
    .choose_condition {
      padding: 0 10px;
      cursor: pointer;
    }
  }
}
/deep/.el-form {
  .el-input {
    width: 240px !important;
  }
}
/deep/.el-dialog {
  .el-dialog__body {
    padding: 10px;
  }
  .el-form {
    margin-bottom: -20px;
  }
}
</style>
