<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/alldevice' }">设备管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{ $route.params.type === 'edit' ? '修改设备' : '新增设备' }}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form inline :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
        <el-form-item label="设备Mac" prop="deviceMac">
          <el-input size="small" v-model="ruleForm.deviceMac" clearable  placeholder="请输入设备Mac"></el-input>
        </el-form-item>
        <el-form-item label="设备名称" prop="name">
          <el-input size="small" v-model="ruleForm.name" clearable placeholder="请输入设备名称"></el-input>
        </el-form-item>
        <el-form-item label="产品型号" prop="productId">
          <el-autocomplete :disabled="$route.params.type === 'edit'" class="inline-input" v-model="productName" :fetch-suggestions="querySearch" placeholder="请输入选择型号" @blur="mustSel(ruleForm.productId,'productName')" @select="handleSelect" clearable></el-autocomplete>
        </el-form-item>
       <el-form-item label="选择公司" prop="enterpriseId">
          <el-autocomplete class="inline-input" v-model="enterpriseName" :fetch-suggestions="queryEnterprise" placeholder="请输入选择公司" @blur="mustSel(ruleForm.enterpriseId,'enterpriseName')" @select="enterpriseHandleSelect" clearable></el-autocomplete>
        </el-form-item>

        <!-- <el-col :span="24">
          <el-col :span="7"> -->
            <el-form-item label="设备地址" prop="province">
              <el-select v-model="ruleForm.province" placeholder="请选择省区" @change="changeProvince(ruleForm.province)" style="width: 100%" filterable remote clearable reserve-keyword>
                <el-option v-for="province in provinces" :key="province.id" :label="province.name" :value="province.code"></el-option>
              </el-select>
            </el-form-item>
          <!-- </el-col>
          <el-col :span="8"> -->
            <el-form-item label="城市" prop="city">
              <el-select v-model="ruleForm.city" placeholder="请选择市区" @change="changeCity(ruleForm.city)" style="width: 100%" filterable remote clearable reserve-keyword>
                <el-option v-for="city in cities" :key="city.id" :label="city.name" :value="city.code"></el-option>
              </el-select>
            </el-form-item>
          <!-- </el-col>
          <el-col :span="6"> -->
            <el-form-item label="地区/县" prop="country">
              <el-select clearable v-model="ruleForm.country" placeholder="请选择地区" @change="changeCountry(ruleForm.country)">
                <el-option v-for="country in counties" :key="country.id" :label="country.name" :value="country.code"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        <!-- </el-col>
        <el-col :span="24"> -->
          <el-form-item label="详细地址" prop="deviceAddress">
            <el-input maxlength="300" @keyup.enter.native="deviceAddress" @blur="deviceAddress" clearable v-model="ruleForm.deviceAddress" placeholder="请填写详细地址" style="width: 300px"></el-input>
          </el-form-item>
        <!-- </el-col> -->
        <div style="font-size: 12px;margin:0 0 10px 100px">提示：如果定位有误，请拖动蓝色图标到准确位置</div>
        <div id="container" style="width:80%; height:400px; margin: 0 0 20px 100px"></div>

        <el-form-item>
          <el-button type="primary" @click="saveForm('ruleForm')">保存</el-button>
          <el-button type="primary" @click="cancelForm">返回</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
import AMap from 'AMap' // 引入高德地图
var map, marker
export default {
  data() {
    return {
      ruleForm: {
        deviceMac: '',
        deviceModel: '',
        productId: '',
        name: '',
        province: '',
        city: '',
        country: '',
        longitude: '',
        latitude: '',
        deviceAddress: '',
        enterpriseId: ''
      },
      rules: {
        deviceMac: [{ required: true, message: '请输入设备Mac地址', trigger: 'blur' }],
        productId: [{ required: true, message: '请输入选择型号', trigger: 'change' }],
        enterpriseId: [{ required: true, message: '请输入选择公司', trigger: 'change' }],
        name: [{ required: true, message: '请输入设备名称', trigger: 'blur' }],
        province: [{ required: true, message: '请输入省份', trigger: 'blur' }],
        city: [{ required: true, message: '请输入城市', trigger: 'blur' }],
        country: [{ required: true, message: '请输入地区', trigger: 'blur' }],
        deviceAddress: [{ required: true, message: '请输入详细地址', trigger: 'blur' }],
      },
      restaurants: [],
      productName: '',
      productId: '',

      //
      enterpriseName: '',

      // 地图

      cityData: [],
      provinces: [],
      cities: [],
      counties: [],
      hideUploadEdit: false,
      arriveCoor: [118.644052, 32.067512], //定位坐标
      arrive: '', //具体信息
      provinceName: '',
      cityName: '',
      countryName: ''
    }
  },

  methods: {
    mustSel(id, name) {
      this.$mustSel(this, id, name)
    },
    handleSelect(item) {
      console.log(item)
      this.productName = item.name
      this.ruleForm.productId = item.id
      // this.isSelect = true // 是否选择了数据
    },
    enterpriseHandleSelect(item) {
      console.log(item)
      this.enterpriseName = item.name
      this.ruleForm.enterpriseId = item.id
    },
    // 设备型号联想查询
    querySearch(queryString, cb) {
      this.associateQuery(queryString, cb, '/product/query/', 'httpGet')
    },
    // 公司联想查询
    queryEnterprise(queryString, cb) {
      this.associateQuery(queryString, cb, '/basic/enterprise/query', 'httpPost')
    },

    //联想查询
    associateQuery(queryString, cb, url, method) {
      var params = { size: 10, page: 1, total: '', state: 1, name: queryString }
      this[method](url, params, null, true).then(res => {
        res.rows.map(i => (i.value = i.name))
        this.restaurants = res.rows
        cb(this.restaurants)
      })
    },
    // 保存
    saveForm(formName) {
      this.$refs[formName].validate(valid => {
        console.log(this.ruleForm)
        if (valid) {
          let url = this.$route.params.type === 'add' ? '/device/add' : '/device/mod/' + this.ruleForm.id
          let msg = this.$route.params.type === 'add' ? '添加成功' : '修改成功'
          let params = Object.assign({}, this.ruleForm)
          params.province = this.provinceName
          params.city = this.cityName
          params.country = this.countryName
          console.log(params)
          this.httpPost(url, params).then(res => {
            this.$message.success(msg)
            this.$router.back()
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 返回
    cancelForm() {
      this.$router.back()
    },

    // 地图
    // 地图
    // 地图
    async getDictionary(parentCode, type) {
      const params = { parentCode: parentCode }
      let ret = await this.httpGet('/dict/query_by_code', params, null, true)
      return ret
    },
    // 省份切换
    async changeProvince(province) {
      // this.provinceName
      let res = await this.getDictionary(province, 2)
      console.log(res)
      this.cities = res
      this.getCityName(province, 2, this.provinces)
    },
    // 城市切换
    async changeCity(city) {
      let res = await this.getDictionary(city, 3)
      console.log(res)
      this.counties = res
      this.getCityName(city, 3, this.cities)
    },
    // 地区切换
    changeCountry(country) {
        this.getCityName(country, 1, this.counties)
    },

    getCityName(code, type, arr) {
      const index = arr.findIndex(item => item.code === code)
      // console.log(arr[index].name)
      if (type === 2) this.provinceName = arr[index].name
      if (type === 3) this.cityName = arr[index].name
      if (type === 1) this.countryName = arr[index].name
    },

    // 获取完整地址
    async deviceAddress() {
      const that = this
      if (that.ruleForm.deviceAddress === '') return
      if (that.provinceName === '') return that.$message.warning('请先选择省份')
      if (that.cityName === '') return that.$message.warning('请先选择城市')
      if (that.countryName === '') return that.$message.warning('请先选择地区')
      let address = that.ruleForm.deviceAddress
      address = address.replace(/#/g, '栋')
      let res = await axios.get(`https://restapi.amap.com/v3/geocode/geo?address=' + ${that.provinceName + that.cityName + that.countryName + that.address} + '&key=630c9b58540b5b07ad9557e3f6c654fa`)
      console.log(res)
      // axios.get('https://restapi.amap.com/v3/geocode/geo?address=' + address + '&key=630c9b58540b5b07ad9557e3f6c654fa').then(res => {
      //   console.log(res)
      let province = ''
      let provincesCode
      let city = ''
      let citiesCode
      let country = ''
      let countiesCode
      that.provinceName = province = res.geocodes[0].province
      that.cityName = city = res.geocodes[0].city.length > 1 ? res.geocodes[0].city : province
      that.countryName = country = res.geocodes[0].district
      for (var i = 0; i < that.provinces.length; i++) {
        if (province == that.provinces[i].name) {
          provincesCode = that.provinces[i].code
          that.ruleForm.province = provincesCode
          let resData = await that.getDictionary(that.provinces[i].code, 2)
          // console.log(resData)
          that.cities = resData
        }
      }
      //   setTimeout(() => {
      for (var j = 0; j < that.cities.length; j++) {
        if (city == that.cities[j].name) {
          citiesCode = that.cities[j].code
          that.ruleForm.city = citiesCode
          let resData = await that.getDictionary(that.cities[j].code, 3)
          // console.log(resData);
          that.counties = resData
        }
      }
      //   }, 1000)
      //   setTimeout(() => {
      for (var k = 0; k < that.counties.length; k++) {
        if (country == that.counties[k].name) {
          that.ruleForm.country = that.counties[k].code
        }
      }
      //   }, 2000)
      if (res.geocodes[0]) {
        const arriveCoor = res.geocodes[0].location.split(',')
        that.arriveCoor = [arriveCoor[0], arriveCoor[1]]
        that.ruleForm.longitude = arriveCoor[0]
        that.ruleForm.latitude = arriveCoor[1]
        that.mapDraw(that.arriveCoor)
      }
      // })
    },

    // 地图选址
    mapDraw(arriveCoor) {
      map = new AMap.Map('container', {
        //container是嵌地图div的id
        resizeEnable: true, //是否监控地图容器尺寸变化
        zoom: 16, //初始化地图层级
        center: this.arriveCoor //初始化地图中心点
      })
      // 定位点
      var _this = this

      AMap.plugin('AMap.ToolBar', () => {
        var toolbar = new AMap.ToolBar()
        map.addControl(toolbar)
      })
      // // 输入提示
      // AMap.plugin('AMap.Autocomplete', function() {
      //   // 实例化Autocomplete
      //   var autoOptions = {
      //     //city 限定城市，默认全国
      //     city: '全国'
      //   }
      //   var autoComplete = new AMap.Autocomplete(autoOptions)
      //   autoComplete.search(_this.ruleForm.deviceAddress, function(status, result) {
      //     // 搜索成功时，result即是对应的匹配数据
      //   })
      // })
      marker = new AMap.Marker({
        icon: '', //图片ip
        imageSize: '20px',
        position: this.arriveCoor,
        offset: new AMap.Pixel(-13, -30),
        // 设置是否可以拖拽
        draggable: true,
        cursor: 'move',
        // 设置拖拽效果
        raiseOnDrag: true
      })
      marker.setMap(map)

      AMap.event.addListener(marker, 'dragend', async e => {
        // console.log(marker.getPosition())
        this.arriveCoor = [marker.getPosition().lng, marker.getPosition().lat]
        let res = await axios.get(`https://restapi.amap.com/v3/geocode/regeo?location=${marker.getPosition().lng},${marker.getPosition().lat}&key=630c9b58540b5b07ad9557e3f6c654fa&radius=1000&extensions=all`)
        console.log(res)
        let province = ''
        let provincesCode
        let city = ''
        let citiesCode
        let country = ''
        let countiesCode
        this.provinceName = province = res.regeocode.addressComponent.province
        this.cityName = city = res.regeocode.addressComponent.city.length > 1 ? res.regeocode.addressComponent.city : province
        this.countryName = country = res.regeocode.addressComponent.district
        for (var i = 0; i < this.provinces.length; i++) {
          if (province == this.provinces[i].name) {
            provincesCode = this.provinces[i].code
            this.ruleForm.province = provincesCode
            let resData = await this.getDictionary(this.provinces[i].code, 2)
            // console.log(resData)
            this.cities = resData
          }
        }
        // setTimeout(() => {
        for (var j = 0; j < this.cities.length; j++) {
          if (city == this.cities[j].name) {
            citiesCode = this.cities[j].code
            this.ruleForm.city = citiesCode
            let resData = await this.getDictionary(this.cities[j].code, 3)
            this.counties = resData
          }
        }
        // }, 1000)
        // setTimeout(() => {
        for (var k = 0; k < this.counties.length; k++) {
          if (country == this.counties[k].name) {
            this.ruleForm.country = this.counties[k].code
          }
        }
        // }, 2000)
        let address = res.regeocode.formatted_address
        let index = address.indexOf(this.countryName)
        // console.log(address.slice(index + this.countryName.length))
        this.ruleForm.deviceAddress = address.slice(index + this.countryName.length)
        if (res.regeocode.pois[0]) {
          this.arriveCoor = res.regeocode.pois[0].location.split(',')
          this.ruleForm.longitude = arriveCoor[0]
          this.ruleForm.latitude = arriveCoor[1]
        }
      })
    }
  },

  async mounted() {
    if (!this.$route.params.type) this.$router.back()
    if (this.$route.params.type === 'edit') {
      let {info} = this.$route.params
      console.log(info)
      this.ruleForm = info
      this.productName = info.productName
      this.enterpriseName = info.enterpriseName
      this.provinceName = info.province
      this.cityName = info.city
      this.countryName = info.country
    }
    this.mapDraw(this.arriveCoor)
    // this.mapCoor(this.arriveCoor)
    let res = await this.getDictionary('system.address', 1)
    this.provinces = res
  }
}
</script>

<style lang="scss" scoped>
.el-form-item__content {
  & > div {
    width: 240px !important;
  }
}
</style>
