<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>公司管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="营业中" name="inOperation">
          <EnterpriseTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @edit="edit" @editResource="editResource" @disable="disable" />
        </el-tab-pane>

        <el-tab-pane label="歇业中" name="outOperation">
          <EnterpriseTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @enable="enable" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import EnterpriseTable from '@/views/base/company/EnterpriseTable'
export default {
  components: { EnterpriseTable },
  data() {
    return {
      activeName: 'inOperation',
      queryForm: {
        name: '',
        state: 1, //1:营业中，0:歇业中
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      formLabelWidth: '120px'
    }
  },
  methods: {
    getPage(page) {
      this.queryForm.page = page
      this.loadData()
    },
    getSearch(params) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editEnterprise', name: 'EditEnterprise', params: { type: 'add'} })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editEnterprise', name: 'EditEnterprise', params: { type: 'edit', info } })
    },
    editResource(info) {
      this.$router.push({ path: '/editResource', name: 'EditResource', params: { type: 'edit', info } })
    },

    disable(id) {
      this.$confirm('确定要使该公司歇业?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/basic/enterprise/disable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '歇业成功!' })
            this.handleClick()
          })
        })
        .catch(() => {})
    },

    enable(id) {
      this.$confirm('确定要该公司开始营业?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/basic/enterprise/enable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '启用成功!' })
            this.handleClick()
          })
        })
        .catch(() => {})
    },

    handleClick(tab, event) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    loadData() {
      let params = Object.assign({}, this.queryForm)
      params.state = this.activeName === 'inOperation' ? 1 : 0
      this.httpPost('/basic/enterprise/query', params, null, true).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
