<template>
  <div>
    <el-form ref="queryInfo" :model="queryInfo" label-width="120px" class="form_border">
      <el-form-item label="商品中文名" prop="name">
        <el-input v-model="queryInfo.name" placeholder="请输入商品中文名"></el-input>
      </el-form-item>
      <el-form-item label="商品英文名" prop="englishName">
        <el-input v-model="queryInfo.englishName" placeholder="请输入商品英文名"></el-input>
      </el-form-item>

      <el-form-item class="select_btn">
        <el-button type="primary" @click="search">搜索</el-button>
      </el-form-item>
    </el-form>
    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <el-button @click.stop="add" icon="el-icon-plus">新增商品</el-button>
        <div class="btn_cont">
          <div class="choose_btn _color"></div>
        </div>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column label="缩略图">
          <template slot-scope="scope">
            <img width="80" :src="imgPath(scope.row.picture)" alt="" />
          </template>
        </el-table-column>
        <el-table-column prop="name" label="商品中文名"> </el-table-column>
        <el-table-column prop="englishName" label="商品英文名"> </el-table-column>
        <el-table-column prop="sellPrice" label="零售价(元)"> </el-table-column>
        <el-table-column prop="sellAmount" label="销售金额"></el-table-column>
        <el-table-column prop="refundAmount" label="退款金额"></el-table-column>
        <el-table-column prop="sellCount" label="订单数"></el-table-column>
        <el-table-column prop="remark" label="备注"> </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="160">
          <template slot-scope="scope">
            <el-link v-if="scope.row.state==1" :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
            <el-link v-if="scope.row.state==1" :underline="false" type="danger" @click.stop="del(scope.row.id)">停用</el-link>
            <el-link v-if="scope.row.state==0" :underline="false" type="primary" @click.stop="enable(scope.row.id)">启用</el-link>
            <!-- 上 下 架 -->
            <!--<el-link v-if="scope.row.shelveStatus === 1" type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row, 1)"><i style="color: #f00">下架</i></el-link>-->
            <!-- <el-link type="primary" :underline="false" slot="reference" @click.stop="putaway(scope.row)">选择机器</el-link> -->
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryInfo.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryInfo, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryInfo, changePage)">尾页</span>
        <el-pagination background :total="queryInfo.total" :current-page.sync="queryInfo.page" :page-size.sync="queryInfo.size" @current-change="changePage" layout="total, prev, pager, next, jumper" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['tableData', 'queryForm'],
  data() {
    return {
      queryInfo: this.queryForm
    }
  },
  methods: {
    imgPath(mainPic) {
      if (!mainPic) return ''
      return this.$picDownload + mainPic.substring(2, mainPic.length - 2)
    },
    changePage() {
      this.$emit('getPage', this.queryInfo.page)
    },
    putaway(info) {
      this.$emit('getPutaway', info)
    },
    search() {
      this.$emit('getSearch', this.queryInfo)
    },
    add() {
      this.$emit('add')
    },
    edit(info) {
      this.$emit('edit', info)
    },
    del(id) {
      this.$emit('del', id)
    },
    enable(id) {
      this.$emit('enable', id)
    }
  },
  watch: {}
}
</script>

<style lang="scss" scoped></style>
