<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>资源管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!--  -->
        <el-tab-pane label="使用中" name="inOperation">
          <ResourceTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @edit="edit" @disable="disable" />
        </el-tab-pane>

        <el-tab-pane label="停用中" name="outOperation">
          <ResourceTable :tableData="tableData" :queryForm="queryForm" @getSearch="getSearch" @getPage="getPage" @add="add" @enable="enable" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import ResourceTable from '@/views/base/resource/ResourceTable'
export default {
  components: { ResourceTable },
  data() {
    return {
      activeName: 'inOperation',
      queryForm: {
        name: '',
        code: '',
        state: 1, //1:使用中，0:禁用中
        size: 10,
        page: 1,
        total: ''
      },
      tableData: [],
      formLabelWidth: '120px'
    }
  },
  methods: {
    getPage(page) {
      this.queryForm.page = page
      this.loadData()
    },
    getSearch(params) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    // 新增
    add() {
      this.$router.push({ path: '/editResources', name: 'EditResources', params: { type: 'add' } })
    },

    // 修改
    edit(info) {
      this.$router.push({ path: '/editResources', name: 'EditResources', params: { type: 'edit', info } })
    },

    disable(id) {
      this.$confirm('确定停用该资源?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/basic/resource/disable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '停用成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    enable(id) {
      this.$confirm('确定启用该资源?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/basic/resource/enable/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '启用成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    handleClick(tab, event) {
      this.queryForm.total = ''
      this.queryForm.page = 1
      this.loadData()
    },
    loadData() {
      let params = Object.assign({}, this.queryForm)
      params.state = this.activeName === 'inOperation' ? 1 : 0
      this.httpPost('/basic/resource/query', params).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  & > div {
    width: 240px !important;
  }
}
</style>
